<!--
 * @Author: linxu
 * @Date: 2022-08-19 16:54:50
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-01 10:57:21
 * @Description: 劣化分析
-->
<template>
  <div>
    <!-- <BMainTopComponent :isShowBackBtn="true" /> -->
    <el-container>
      <el-main>
        <div class="deterioration-analysis">
          <el-tabs v-model="activeName" type="border-card">
            <el-tab-pane label="水箱劣化分析" name="water">
              <curve ref="waterCurve" :tab="activeName" id="water" api="getGenLieHuaSxData"></curve>
            </el-tab-pane>
            <el-tab-pane label="机油滤清器劣化分析" name="oil">
              <curve ref="oilCurve" :tab="activeName" id="oil" api="getGenLieHuaJyData"></curve
            ></el-tab-pane>
            <el-tab-pane label="电瓶劣化分析" name="battery">
              <curve
                ref="batteryCurve"
                :tab="activeName"
                id="battery"
                api="getGenLieHuaDpData"
              ></curve
            ></el-tab-pane>
          </el-tabs>
        </div>
      </el-main>
      <!-- <toggle-crew ></toggle-crew> -->
    </el-container>
  </div>
</template>

<script>
import Curve from './Curve.vue'
import ToggleCrew from '@/components/toggleCrew/list'

export default {
  name: 'DeteriorationAnalysis',
  components: {
    Curve,
    ToggleCrew
  },
  data() {
    return {
      activeName: 'water'
    }
  },
  props: [''],
  // inject: [''],
  // provide() {
  //   return {}
  // },
  computed: {},
  watch: {
    // 跳转路由对应页签与当前页签相同，组件复用，未重新查询数据
    $route: {
      handler: function (to, from) {
        if (from) {
          this.initData()
        }
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    initData() {
      switch (this.activeName) {
        case 'water':
          this.$refs.waterCurve.getCurveData()
          break
        case 'oil':
          this.$refs.oilCurve.getCurveData()
          break
        case 'battery':
          this.$refs.batteryCurve.getCurveData()
          break
        default:
          break
      }
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {}
}
</script>
<style lang="less" scoped>
.deterioration-analysis {
  /deep/ .el-tabs--border-card {
    background: transparent;
    .el-tabs__header {
      background: transparent;
    }
  }
}
</style>



