<!--
 * @Autor: Demon3443002624@qq.com
 * @Date: 20220311 周五
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-19 15:25:05
 * @Description: 维保策略（新）页面组件-可编辑列表组件
-->
<template>
  <div>
    <!-- <el-row type="flex" justify="end">
      <el-button class="add-btn" size="mini" @click="handleAdd()">{{ labelText.add }}</el-button>
    </el-row> -->
    <el-table
      ref="myTable"
      :data="table_data.filter((data) => data.MaintenanceItemId !== -1)"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column v-if="radio" type="index" width="50"></el-table-column>
      <el-table-column v-if="selection" type="selection" width="55"></el-table-column>
      <el-table-column
        align="center"
        v-for="(item, index, key) in table_columns"
        :item="item"
        :key="key"
        :index="index"
        :label="item.label"
      >
        <template slot-scope="scope">
          <el-input
            v-if="scope.row.edit && item.prop === 'ItemContent'"
            size="small"
            v-model="scope.row[item.prop]"
            :placeholder="'请输入' + item.label"
          ></el-input>
          <el-input-number
            v-if="scope.row.edit && item.prop !== 'ItemContent'"
            v-model="scope.row[item.prop]"
            :min="0"
            controls-position="right"
            :placeholder="'请输入' + item.label"
          ></el-input-number>
          <!-- 
            :max="item.prop === 'operatingTime' ? 10000 : 1000" -->
          <span v-if="!scope.row.edit">{{ scope.row[item.prop] }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <!-- 全局控制的编辑 -->
          <!-- <div v-if="is_edit" style="display: inline-block">
            <el-button
              size="mini"
              v-if="!scope.row.edit"
              @click="handleEdit(scope.$index, scope.row)"
              type="primary"
              >{{ labelText.edit }}</el-button
            >
            <el-button
              size="mini"
              type="primary"
              :plain="true"
              v-if="scope.row.edit"
              @click="handleSave(scope.$index, scope.row)"
              >{{ labelText.save }}</el-button
            >
          </div> -->
          <!-- 全局控制删除 -->
          <el-button
            size="mini"
            v-if="is_delete"
            :plain="true"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            >{{ labelText.delete }}</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { data as _data } from './defaultData'
import _ from 'lodash'

export default {
  data() {
    return {
      new_date_json: {}, //数据结构
      multipleSelection: [], //复选框，数据
      is_edit: true, //是否可编辑
      is_delete: true, //是否可删除
      selection: false, //是否需要复选框
      radio: true, //单选变色
      space_color: false, //隔行变色
      //表格数据
      table_data: []
    }
  },
  props: {
    // 添加、编辑、删除
    labelText: {
      type: Object,
      default: (_) => {
        return {
          add: '添加项目',
          edit: '编辑',
          save: '确定',
          delete: '删除'
        }
      }
    },
    // 表头信息
    table_columns: {
      type: Array,
      default: (_) => {
        return _data.table_columns
      }
    },
    // 表格数据
    dataArray: {
      type: Array,
      default: (_) => {
        return _data.dataArray
      }
    }
  },
  watch: {
    space_color: function () {
      //监听数据变化
      this.$nextTick(function () {
        /////方法
        this.tableRowClassName()
      })
    },
    table_data: {
      handler: function (nv, ov) {
        console.log(nv, ov)
        this.$emit('dataChange', { data: nv })
        //监听数据变化f
        this.$nextTick(() => {
          this.tableRowClassName()
        })
      },
      deep: true
    }
  },
  methods: {
    //隔行变色
    tableRowClassName() {
      //选取DOM节点
      var trs = this.$refs.myTable.$el.getElementsByTagName('tbody')[0].getElementsByTagName('tr')
      for (var i in trs) {
        if (i % 2 == 0) {
          //当隔行变色未true时改变颜色
          if (this.space_color) {
            trs[i].style.backgroundColor = '#f0f9eb'
          } else {
            trs[i].style.backgroundColor = ''
          }
        }
      }
    },

    //多选框
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    //编辑
    handleEdit(index, row) {
      row.edit = true
    },
    //删除
    handleDelete(index, row) {
      console.log(index, row)
      const findIndex = this.table_data.findIndex((item) => item == row)

      if (!!row.add) {
        this.table_data.splice(findIndex, 1)
        return
      }
      this.$confirm('', '', {
        message: '是否确定<span style="color:red;font-weight:bold">删除</span>该项目？',
        dangerouslyUseHTMLString: true
      })
        .then((_) => {
          const newData = {
            ...row,
            MaintenanceItemId: -1
          }
          this.table_data.splice(findIndex, 1, newData)
        })
        .catch(() => {
          return false
        })
    },
    //保存
    handleSave(index, row) {
      row.edit = false

      delete this.table_data[index].add

      this.$message({
        message: '操作成功！',
        type: 'success'
      })
    },
    handleAdd() {
      var addDataJson = {}
      for (var key in this.new_date_json) {
        if (key === 'edit') {
          delete addDataJson[key]
        } else if (key === 'add') {
          delete addDataJson[key]
        } else {
          addDataJson[key] = null
        }
      }
      addDataJson.edit = true
      addDataJson.add = true
      console.log('handleAdd', addDataJson)
      this.table_data.push(addDataJson)
      this.$nextTick(() => {
        this.$emit('addDataItem')
      })
    },
    //初始化编辑属性
    initEditAttribute() {
      var self = this
      var edit = self.edit

      var dataArray = _.cloneDeep(this.dataArray)

      if (dataArray.length > 0) {
        //添加编辑事件
        for (var index in dataArray) {
          dataArray[index]['edit'] = true
          this.table_data.push(dataArray[index])
        }

        if (Object.keys(this.new_date_json).length === 0) {
          //新增时，初始化数据结构
          this.initAddDataJson(dataArray[0])
        }
      }
    },
    initAddDataJson(dataArray) {
      //新增时，初始化数据结构
      var dataJson = dataArray
      var newDateJson = {}
      for (var key in dataJson) {
        if (key === 'edit') {
          newDateJson[key] = 'true'
        } else {
          newDateJson[key] = ''
        }
      }
      newDateJson['add'] = true
      this.new_date_json = newDateJson
    }
  },
  created() {},
  mounted: function () {
    this.initEditAttribute()
    //确保方法在页面渲染后调用
    this.$nextTick(function () {
      /////方法
      this.tableRowClassName()
    })
  }
}
</script>
<style lang="less" scoped>
.add-btn {
  margin: 6px;
}
</style>



