<!--
 * @Author: linxu
 * @Date: 2021-11-18 13:40:39
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 16:58:55
 * @Description: 机组报告详情页面
-->
<template>
  <!-- <InteractionTestReportDetail v-else-if="selectTab === '1' && pageFlag === '0'" /> -->
  <div>
    <!-- <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    /> -->
    <el-container>
      <el-main>
        <div id="crewreport-detail">
          <!-- <toggle-crew></toggle-crew> -->
          <div class="creat-info-box">
            <!-- <div class="content-title">机组报告</div> -->
            <div class="tab-box">
              <el-button
                v-for="item in tabs"
                :key="item.value"
                :class="['status-tab', { 'select-tab': selectTab == item.value }]"
                @click="changeTab(item.value)"
                >{{ item.label }}</el-button
              >
            </div>
            <!-- 出厂试验报告 交付试验报告详情 -->
            <DeliveryTestReportDetail
              ref="DeliveryTestReportDetail"
              v-if="(selectTab === '0' || selectTab === '1') && pageFlag === '0'"
              :recordInfo="recordInfo"
              @handleBack="handleBack"
            />
            <template v-else>
              <DeliveryTestReport
                ref="DeliveryTestReport"
                v-if="selectTab === '0'"
                @showReport="showReport"
              ></DeliveryTestReport>

              <InteractionTestReport
                ref="InteractionTestReport"
                v-else-if="selectTab === '1'"
                @showReport="showReport"
              ></InteractionTestReport>

              <TimeMatrixReport
                ref="TimeMatrixReport"
                v-else-if="selectTab === '2'"
              ></TimeMatrixReport>

              <OperationReport
                ref="OperationReport"
                v-else-if="selectTab === '3'"
              ></OperationReport>

              <OperationalReliabilityReport
                ref="OperationalReliabilityReport"
                v-else-if="selectTab === '4'"
              ></OperationalReliabilityReport>

              <StartingReliabilityReport
                ref="StartingReliabilityReport"
                v-else-if="selectTab === '5'"
              ></StartingReliabilityReport>
              <!-- <iframe-component :linkUrl='linkUrl' /> -->
            </template>
          </div>
        </div>
      </el-main>
      <!-- <toggle-crew></toggle-crew> -->
    </el-container>
  </div>
</template>

<script>
import ToggleCrew from '@/components/toggleCrew/list'
import IframeComponent from '@/views/module/iframeComponent'
import { mapGetters } from 'vuex'
import DeliveryTestReport from './components/DeliveryTestReport'
import InteractionTestReport from './components/InteractionTestReport'
import TimeMatrixReport from './components/TimeMatrixReport'
import OperationReport from './components/OperationReport'
import OperationalReliabilityReport from './components/OperationalReliabilityReport'
import StartingReliabilityReport from './components/StartingReliabilityReport'
import DeliveryTestReportDetail from './components/DeliveryTestReportDetail'
import InteractionTestReportDetail from './components/InteractionTestReportDetail'

export default {
  name: 'crewreportDetail',
  components: {
    ToggleCrew,
    IframeComponent,
    DeliveryTestReport,
    InteractionTestReport,
    TimeMatrixReport,
    OperationReport,
    OperationalReliabilityReport,
    StartingReliabilityReport,

    DeliveryTestReportDetail,
    InteractionTestReportDetail
  },
  data() {
    return {
      tabs: [
        {
          label: '出厂试验报告',
          value: '0',
          linkUrl:
            'http://www.fortrustyun.com:8022/Genset/TestReportFactory?condition=&mpage=0&ishow=0&gid='
        },
        {
          label: '交付试验报告',
          value: '1',
          linkUrl:
            'http://www.fortrustyun.com:8022/Genset/TestReportDelivery?condition=&mpage=0&ishow=0&gid='
        },
        {
          label: '起动可靠性分析报告',
          value: '5'
        },
        {
          label: '功率时间矩阵分析报告',
          value: '2',
          linkUrl:
            'http://www.fortrustyun.com:8022/Genset/GeneratorSetRecordDailyPowerTime?condition=&mpage=0&ishow=0&gid='
        },
        {
          label: '运行报告',
          value: '3',
          linkUrl:
            'http://www.fortrustyun.com:8022/Genset/GensetRunReport?condition=&mpage=0&ishow=0&gid='
        },
        {
          label: '运行可靠性报告',
          value: '4',
          linkUrl:
            'http://www.fortrustyun.com:8022/Genset/GensetRunReliabilityReport?condition=&mpage=0&ishow=0&gid='
        }
      ],
      selectTab: this.$route.query.selectTab || '0',
      pageFlag: this.$route.query.pageFlag || '1',
      recordInfo: null
    }
  },
  computed: {
    ...mapGetters(['selectCrew']),
    linkUrl() {
      return this.tabs.filter((item) => item.value === this.selectTab)[0].linkUrl
    }
  },
  watch: {
    $route: {
      handler: function (newVal, oldVal) {
        this.selectTab = this.$route.query.selectTab || '0'
        this.pageFlag = this.$route.query.pageFlag || '1'
        console.log('$route', newVal, this.selectTab === '0', this.pageFlag === '0')
      },
      // 深度观察监听
      deep: true
    }
  },
  methods: {
    initData() {
      if (this.pageFlag == '0') {
        this.$refs.DeliveryTestReportDetail.getCurveData()
        return
      }
      if (this.selectTab == 0) {
        this.$refs.DeliveryTestReport.queryTableData()
      } else if (this.selectTab == 1) {
        this.$refs.InteractionTestReport.queryTableData()
      } else if (this.selectTab == 2) {
        this.$refs.TimeMatrixReport.getReportData()
      } else if (this.selectTab == 3) {
        this.$refs.OperationReport.initData()
      } else if (this.selectTab == 4) {
        this.$refs.OperationalReliabilityReport.initData()
      } else {
        this.$refs.StartingReliabilityReport.handleSearch()
      }
    },
    changeTab(val) {
      console.log(val, typeof val)
      this.selectTab = val
      this.pageFlag = '1'
      // this.$router.push({ path: '/equipment/crewreport-detail', query: { selectTab: val } })
    },
    handleBackClick() {
      this.$router.push({ path: '/equipment/crewinfo' })
    },
    // 查看报告
    showReport(val) {
      this.selectTab = String(val.selectTab)
      this.pageFlag = String(val.pageFlag)
      this.recordInfo = val.record
      console.log(val, this.selectTab, this.pageFlag, this.recordInfo)
    },
    handleBack() {
      this.pageFlag = '1'
    }
  }
}
</script>

<style lang="less" scoped>
#crewreport-detail {
  .creat-info-box {
    flex: 1;
  }
  /deep/ .tab-box {
    margin-bottom: 16px;
    .status-tab {
      padding: 8px 12px;
    }
    .select-tab {
      background-color: #1abc9c !important;
      color: #fff !important;
      border: 0;
    }
    + div {
      // padding-bottom: 16px;
    }
  }
}
</style>



