<!--
 * @Autor: linxu
 * @Date: 2022-01-18 16:31:34
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 16:53:45
 * @Description: 运行报告
-->
<template>
  <div class="operation-report-main">
    <div class="search-box">
      <div class="search-item">
        <span>筛选:</span>
        <el-date-picker
          v-model="selectYear"
          type="year"
          placeholder="选择年"
          value-format="yyyy"
          :picker-options="pickerOptions"
          placement="bottom-start"
        >
        </el-date-picker>
      </div>
      <el-button type="primary" @click="handleSearch()">搜索</el-button>
      <el-button type="primary" icon="el-icon-download" @click="downloadFile">下载</el-button>
    </div>
    <div id="descriptions-box" class="descriptions-box">
      <p class="title">{{ title }}</p>
      <el-descriptions class="descriptions-items" title="" :column="2" :size="size" border>
        <template v-for="(item, index) in descriptionsItems">
          <el-descriptions-item :key="index" :span="item['size']">
            <template slot="label">{{ item['label'] }}</template>
            <div class="description-content-box">
              {{ (dataSource && dataSource[item['key']]) || '-' }}
              <span v-if="dataSource && dataSource[item['key']] && item['unit']">{{
                item['unit']
              }}</span>
            </div>
          </el-descriptions-item>
        </template>
      </el-descriptions>

      <!-- 机组运行图例 -->
      <BaseEcharts
        v-if="flag"
        :containerId="containerId"
        :colors="colors"
        :grid="grid"
        :legendData="legendData"
        :xAxisData="xAxisData"
        :yAxisData="yAxisData"
        :seriesData="seriesData"
        :toolboxCfg="toolboxCfg"
      />

      <OperationalReliabilityReportCurve
        ref="OperationalReliabilityReportCurve"
        v-if="title == '机组运行可靠性报告'"
        :year="year"
      />
    </div>
  </div>
</template>

<script>
import { htmlToPdf } from '@/utils/util'
import { mapGetters } from 'vuex'
import dayjs from 'dayjs'
import BaseEcharts from '@/components/BaseEcharts'
import OperationalReliabilityReportCurve from './OperationalReliabilityReportCurve'
const descriptionsItems = [
  {
    label: '机组名称',
    key: 'DisplayName',
    unit: '',
    size: ''
  },
  {
    label: '云猫标识',
    key: 'CollectorToken',
    unit: '',
    size: ''
  },
  {
    label: '品牌',
    key: 'Brand',
    unit: '',
    size: ''
  },
  {
    label: '出厂编号',
    key: 'Serial',
    unit: '',
    size: ''
  },
  {
    label: '发动机',
    key: 'EngineBrand',
    unit: '',
    size: ''
  },
  {
    label: '发电机',
    key: 'GeneratorBrand',
    unit: '',
    size: ''
  },
  {
    label: '采集器',
    key: 'CollectorBrand',
    unit: '',
    size: ''
  },
  {
    label: '控制器',
    key: 'ControllerBrand',
    unit: '',
    size: ''
  },
  {
    label: '累计发电量',
    key: 'TotalYearEnergy',
    unit: 'kwh',
    size: '2'
  },
  {
    label: '累计运行时间',
    key: 'TotalYearAllMinute',
    unit: '分钟',
    size: '2'
  }
]
const colors = ['#5470C6', '#91CC75', '#EE6666']
const echartsItems = [
  {
    label: '发电量',
    key: 'TotalEnergy',
    type: 'bar',
    unit: 'kwh',
    data: [2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3]
  },
  {
    label: '运行时间',
    key: 'TotalTime',
    type: 'bar',
    unit: 'Min',
    data: [2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 6.0, 2.3]
  },
  {
    label: '次数',
    key: 'TotalCount',
    type: 'line',
    data: [2.0, 2.2, 3.3, 4.5, 6.3, 10.2, 20.3, 23.4, 23.0, 16.5, 12.0, 6.2]
  }
]
export default {
  name: 'OperationReportComponent',
  components: {
    BaseEcharts,
    OperationalReliabilityReportCurve
  },
  props: ['title'],
  data() {
    return {
      size: '',
      descriptionsItems,
      dataSource: null,
      selectYear: new Date().getFullYear().toString(),
      year: new Date().getFullYear().toString(),

      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      },
      // myEchart: null,
      // option
      flag: false, // 更新 BaseEcharts 组件
      toolboxCfg: {
        dataViewIsShow: true,
        magicTypeIsShow: true,
        restoreIsShow: true,
        saveAsImageIsShow: true
      },
      containerId: 'operation-report-echart',
      colors,
      grid: {
        left: '5%',
        right: '8%',
        top: '15%'
      },
      legendData: [],
      xAxisData: [],
      yAxisData: [],
      seriesData: [],

      data: null
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.getData()
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    initData() {
      if (this.title == '机组运行可靠性报告') {
        this.$refs.OperationalReliabilityReportCurve.getData()
      }
    },
    handleSearch() {
      if (!this.selectYear) {
        this.selectYear = this.year
      }
      this.year = this.selectYear
      this.getData()
    },
    async getData() {
      const params = {
        GeneratorSetId: this.selectCrew.ID,
        Year: this.year,
        StartTime: `${this.year}-01`
        // EndTime: dayjs(new Date()).format('YYYY-MM-DD')
      }
      await this.$api.equipment
        .GetGensetRunReport(params, {})
        .then((res) => {
          if (res.Code === 1) {
            this.$set(this, 'data', res.Data)
            this.$set(this, 'dataSource', {
              ...res.Data.Genset,
              TotalYearEnergy: res.Data.TotalYearEnergy,
              TotalYearAllMinute: res.Data.TotalYearAllMinute,
              TotalYearHour: res.Data.TotalYearHour,
              TotalYearMinute: res.Data.TotalYearMinute
            })
          }
        })
        .catch(() => {})

      this.init()
    },
    init() {
      // console.log(this.data)
      this.xAxisData = [
        {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
          data: this.data.TotalMonth
        }
      ]

      const legendData = []
      const yAxisData = []
      const seriesData = []
      echartsItems.forEach((item, index) => {
        legendData.push(item.label)
        // 设置yAxisData
        const yAxisDataItem = {
          type: 'value',
          name: `${item.label} ${item.unit || ''}`,
          min: 0,
          // max: 250,
          position: index === 0 ? 'left' : 'right',
          offset: index >= 2 ? (index - 1) * 50 : 0,
          axisLine: {
            show: true,
            lineStyle: {
              color: colors[index]
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#6E7079',
              opacity: 0.4
            }
          },
          axisLabel: {
            formatter: `{value}`
          }
        }
        yAxisData.push(yAxisDataItem)
        // 设置seriesData
        const seriesDataItem = {
          name: item.label,
          type: item.type,
          yAxisIndex: index,
          data: this.data[item.key]
        }
        seriesData.push(seriesDataItem)
      })
      this.$set(this, 'legendData', legendData)
      this.$set(this, 'yAxisData', yAxisData)
      this.$set(this, 'seriesData', seriesData)
      this.$nextTick(() => {
        this.flag = true
      })
    },
    // 下载
    downloadFile() {
      const props = {
        title: this.title,
        contentId: '#descriptions-box'
      }
      htmlToPdf(props)
    }
  }
}
</script>

<style lang="less" scoped>
.search-box {
  margin-bottom: 16px;
  display: flex;
  justify-content: flex-start;
  background-color: #fff;
  .search-item {
    width: 20%;
    > span {
      margin-right: 8px;
    }
  }
}
.descriptions-box {
  border: 1px solid #ebeef5;
  > p.title {
    line-height: 40px;
    text-align: center;
    font-weight: bold;
    font-size: 22px;
  }
  .descriptions-items {
    margin-bottom: 16px;
  }
  /deep/.el-descriptions {
    .el-descriptions-item__cell {
      text-align: center;
    }
    td.el-descriptions-item__cell {
      width: 35%;
    }
  }
  .description-content-box {
    text-align: center;
    > span {
      margin-left: 2px;
    }
  }

  #operation-report-echart {
    height: 560px;
  }
}

.theme-dark .descriptions-box {
  background-color: #102447;
}
</style>
