<!--
 * @Author: linxu
 * @Date: 2021-11-18 13:40:38
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 16:35:41
 * @Description: 机组信息详情页面
-->
<template>
  <div>
    <!-- <BMainTopComponent :isShowBackBtn="true" /> -->
    <el-container>
      <el-main>
        <div id="crewinfo-detail">
          <!-- <toggle-crew></toggle-crew> -->
          <div class="creat-info-box">
            <!-- <div class="content-title">机组信息</div> -->
            <div class="tab-box">
              <el-button
                v-for="item in tabs"
                :key="item.value"
                :class="['status-tab', { 'select-tab': selectTab == item.value }]"
                @click="changeTab(item.value)"
                >{{ item.label }}</el-button
              >
            </div>
            <crew-info ref="crewInfo" v-if="selectTab == 0"></crew-info>
            <crew-history ref="crewHistory" v-else-if="selectTab == 1"></crew-history>
            <crew-key-parts ref="crewKeyParts" v-else></crew-key-parts>
            <!-- <iframe-component v-else
                        :linkUrl='linkUrl' /> -->
          </div>
        </div>
      </el-main>
      <!-- <toggle-crew></toggle-crew> -->
    </el-container>
  </div>
</template>

<script>
import ToggleCrew from '@/components/toggleCrew/list'
import IframeComponent from '@/views/module/iframeComponent'
import { mapGetters } from 'vuex'
import CrewInfo from './components/crewinfo'
import CrewHistory from './components/crewHistory'
import CrewKeyParts from './components/crewKeyParts'
export default {
  name: 'crewinfoDetail',
  components: {
    ToggleCrew,
    IframeComponent,
    CrewInfo,
    CrewHistory,
    CrewKeyParts
  },
  data() {
    return {
      tabs: [
        {
          label: '机组信息',
          value: 0,
          linkUrl: 'http://www.fortrustyun.com:8022/Genset/Info?condition=&mpage=0&ishow=0&gid='
        },
        {
          label: '机组履历',
          value: 1,
          linkUrl:
            'http://www.fortrustyun.com:8022/Genset/InfoResume?condition=&mpage=0&ishow=0&gid='
        },
        {
          label: '机组关重件',
          value: 2,
          linkUrl: 'http://www.fortrustyun.com:8022/Genset/InfoBom?condition=&mpage=0&ishow=0&gid='
        }
      ],
      selectTab: 0
    }
  },
  computed: {
    ...mapGetters(['selectCrew']),
    linkUrl() {
      return this.tabs.filter((item) => item.value === this.selectTab)[0].linkUrl
    }
  },
  methods: {
    initData() {
      if (this.selectTab == 0) {
        this.$refs.crewInfo.getsingleMchInfo()
      } else if (this.selectTab == 1) {
        this.$refs.crewHistory.queryTableData()
      } else {
        this.$refs.crewKeyParts.queryTableData()
      }
    },
    changeTab(val) {
      this.selectTab = val
    }
  }
}
</script>

<style lang="less" scoped>
#crewinfo-detail {
  .tab-box {
    margin-bottom: 16px;
    .select-tab {
      background-color: #1abc9c !important;
      color: #fff !important;
      border: 0;
    }
  }
  .creat-info-box {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
}
</style>



