    key: '6',
    name: '补机油补油泵',
    tip: '油底壳补油',
    props: 'fpie_006_45',
    openCode: '33',
    closeCode: '34'
  },
  {
    key: '7',
    name: '待滤油箱抽油泵',
    tip: '油底壳抽油',
    props: 'fpie_006_95',
    openCode: '31',
    closeCode: '32'
  }
]

const table_columns = [
  {
    prop: 'ItemContent',
    label: '维保事项',
    width: '100'
  },
  {
    prop: 'DefaultHourSetting',
    label: '运行时间(h)',
    width: '100'
  },
  {
    prop: 'DefaultDaySetting',
    label: '绝对时间(天)',
    width: '100'
  }
]
export default {
  name: 'sensorMonitor',
  inject: ['reload'],
  components: {
    GensetInfo,
    ToggleCrew,
    EditTable,
    ControllerConfiguration
  },
  props: {
    isShow: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      IsVideo: null,
      videoH5Url: null,
      // 开关状态
      switchStatus: {
        '1_flag': false,
        '2_flag': false,
        '3_flag': false,
        '4_flag': false,
        '5_flag': false,
        '6_flag': false,
        '7_flag': false
      },
      resizefun: null,
      animation: null,
      myCharts: [],
      myLiquidfillCharts: [],
      colors: [
        { color: '#f56c6c', percentage: 20 },
        { color: '#e6a23c', percentage: 40 },
        { color: '#5cb87a', percentage: 60 },
        { color: '#1989fa', percentage: 80 },
        { color: '#6f7ad3', percentage: 100 }
      ],
      CollectorTime: '',
      realTimeData: {}, // 实时数据
      GeneratorSetInfo: {},
      IsFirstRequst: true, // 第一次请求

      // 图例数据
      colLeft: 9,
      colRight: 15,
      echart_arr: [
        {
          id: 'monitor-gauge-1',
          des: '燃油油水比重',
          key: 'fpie_006_12',
          value: 0,
          unit: '%',
          min: 0,
          max: 100,
          IsRender: false,
          seriesType: 'gauge',
          axisLineSetting: [
            [0.2, '#228b22'],
            [0.8, '#f59a23'],
            [1, '#d9001b']
            // [1, '#555555']
          ]
        },
        {
          id: 'monitor-gauge-2',
          des: '进油管空气分离器',
          key: 'fpie_006_02',
          value: 0,
          unit: '%',
          min: 0,
          max: 100,
          IsRender: false,
          seriesType: 'gauge',
          axisLineSetting: [
            [0.2, '#228b22'],
            [0.8, '#f59a23'],
            [1, '#d9001b']
            // [1, '#555555']
          ]
        },

        {
          id: 'monitor-liquidfill-1',
          des: '燃油箱余量',
          key: 'fpie_006_22',
          realKey: 'fpie_006_23',
          value: 0,
          unit: 'L',
          min: 0,
          max: 700,
          IsRender: false,
          seriesType: 'liquidFill'
        },
        {
          id: 'monitor-gauge-3',
          des: '油底壳机油液位',
          key: 'fpie_006_32',
          value: 0,
          unit: '%',
          min: 0,
          max: 100,
          IsRender: false,
          seriesType: 'gauge',
          axisLineSetting: [
            [0.2, '#d9001b'],
            [0.8, '#f59a23'],
            [1, '#228b22']
            // [1, '#555555']
          ]
        },

        {
          id: 'monitor-liquidfill-2',
          des: '补机油箱液位',
          key: 'fpie_006_42',
          realKey: 'fpie_006_43',
          value: 0,
          unit: 'L',
          min: 0,
          max: 120,
          IsRender: false,
          seriesType: 'liquidFill'
        },

        {
          id: 'monitor-liquidfill-3',
          des: '待滤油箱液位',
          key: 'fpie_006_92',
          realKey: 'fpie_006_93',
          value: 0,
          unit: 'L',
          min: 0,
          max: 120,
          IsRender: false,
          seriesType: 'liquidFill'
        },
        {
          id: 'monitor-liquidfill-4',
          des: '废液箱液位',
          key: 'fpie_006_62',
          realKey: 'fpie_006_63',
          value: 0,
          unit: 'L',
          min: 0,
          max: 223,
          IsRender: false,
          seriesType: 'liquidFill'
        }
      ],
      loading: true,

      showStrategyItemList: null, // 页面显示维保事项
      editDialigVisible: false, // 维保策略优化弹窗
      GensetSettingDetail: null, // 机组维保策略详情
      table_columns,
      StrategyItemList: null,
      matterListDialigVisible: false, // 更多维保事项弹窗
      showDetailDialigVisible: false, // 维保策略详情弹窗
      configData: null,

      showRecordsDialigVisible: false, // 维保记录弹窗
      searchForm: {
        keyWord: '',
        searchTime: null
        // startDate: null,
        // endDate: null
      },
      pickerTime: {
        disabledDate: (time) => {
          return time.getTime() > Date.now() - 8.64e6
        }
      },
      dataSource: [
        {
          date: '2022-06-10',
          person: '姜波',
          tableData: [
            { name: '维保事项1', data: '运行：500h/480h    绝对：800/480h' },
            { name: '维保事项2', data: '运行：500h/480h    绝对：800/480h' }
          ]
        },
        {
          date: '2022-06-12',
          person: '姜波',
          tableData: [
            { name: '维保事项1', data: '运行：500h/480h    绝对：800/480h' },
            { name: '维保事项2', data: '运行：500h/480h    绝对：800/480h' }
          ]
        }
      ]
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    // 监听选中机组切换
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.initData()
      },
      deep: true
    },
    // 跳转路由对应页签与当前页签相同，组件复用，未重新查询数据
    $route: {
      handler: function (to, from) {
        if (from) {
          this.initData()
        }
      },
      deep: true,
      immediate: true
    }
  },
  mounted() {
    if (this.selectCrew.ID) {
      // 获取设备信息
      this.initData()
      // 窗口变化，图例自动缩放
      this.resizefun = () => {
        this.myCharts.forEach((item) => {
          item.resize()
        })
      }
      window.addEventListener('resize', this.resizefun)
    }
  },
  methods: {
    initData() {
      this.getGeneratorSet()
      this.getPicVideo()
      this.initInterval()
    },

    initInterval() {
      // 获取实时数据
      this.getMchInfo()
      if (this.animation) {
        clearInterval(this.animation)
        this.animation = null
      }
      this.animation = setInterval(() => {
        this.getMchInfo()
      }, 1000 * 3 * 1)
    },

    getGeneratorSet() {
      // 获取设备信息
      // this.$api.screenData.getsingleMchInfo({ id: this.selectCrew.ID }).then((res) => {
      // this.GeneratorSetInfo = res.Data
      // // 维保单元操作按钮
      // this.$set(this, 'ConfigExpandControl', res.Data.ConfigExpandControl)
      // })

      // 查询维保策略以及提醒
      this.$api.screenData
        .GetMaintenanceGensetSettingDetailV2({}, { GensetId: this.selectCrew.ID })
        .then((res) => {
          // console.log(res)
          if (res.Code === 1) {
            // 维保事项
            this.$set(this, 'StrategyItemList', res.Data.Programs)
            this.$set(
              this,
              'showStrategyItemList',
              res.Data.Programs && res.Data.Programs.slice(0, 4)
            )
          }
        })
    },

    // 获取机组图片（数组）和视频
    getPicVideo() {
      this.$api.screenData
        .FindGeneratorSetByMonitor({ id: this.selectCrew.ID })
        .then((resolve) => {
          const Data = resolve.Data
          this.IsVideo = Data.IsVideo
          if (Data.IsVideo) {
            !!Data && (this.videoH5Url = Data.VideoH5Url)
            this.$nextTick(() => {
              this.renderVideo()
            })
          }
        })
        .catch((err) => {
          console.error(
            '设备管理-机组监控-机组监控-获取图片和视频接口 FindGeneratorSetByMonitor ，错误信息：\n',
            err
          )
        })
    },

    // 初始化萤石云视频播放（在刚进入页面时更新一次）
    renderVideo() {
      // 使用 hls 实现
      if (this.videoH5Url) {
        const hls = new Hls()
        hls.attachMedia(this.$refs.myVideo)
        hls.on(Hls.Events.MEDIA_ATTACHED, (_) => {
          hls.loadSource(this.videoH5Url)
          hls.on(Hls.Events.MANIFEST_PARSED, function () {
            this.$refs.myVideo.play()
          })
        })
        // 出错处理
        hls.on(Hls.Events.ERROR, function (event, data) {
          if (data.fatal) {
            switch (data.type) {
              case Hls.ErrorTypes.NETWORK_ERROR:
                // try to recover network error
                console.error('fatal network error encountered, try to recover')
                hls.startLoad()
                break
              case Hls.ErrorTypes.MEDIA_ERROR:
                console.error('fatal media error encountered, try to recover')
                hls.recoverMediaError()
                break
              default:
                // cannot recover
                hls.destroy()
                break
            }
          }
        })
      }
    },

    // 获取实时数据信息
    getMchInfo() {
      this.$api.screenData
        .getsingleMchInfoRealtime({ id: this.selectCrew.ID })
        .then((res) => {
          // console.log('实时数据', res)
          if (res.Code === 1) {
            const Data = res.Data
            this.CollectorTime = formatTime(Data.CollectorTime).format('yyyy-MM-DD HH:mm:ss')
            this.$set(this.GeneratorSetInfo, 'CollectorTime', this.CollectorTime)

            // 渲染实时数据信息
            const nowdata = Data.NowData
            if (Object.keys(nowdata).includes('fpie_001_03')) {
              let TemperatureValue = nowdata['fpie_001_03'].Value
                ? nowdata['fpie_001_03'].Value !== '####'
                  ? nowdata['fpie_001_03'].Value + nowdata['fpie_001_03'].Unit
                  : '-'
                : ''
              this.$set(this.GeneratorSetInfo, 'Temperature', TemperatureValue)
            } else {
              this.$set(this.GeneratorSetInfo, 'Temperature', '')
            }
            if (Object.keys(nowdata).includes('fpie_006_74')) {
              let WindageValue = nowdata['fpie_006_74'].Value
                ? nowdata['fpie_006_74'].Value !== '####'
                  ? nowdata['fpie_006_74'].Value + nowdata['fpie_006_74'].Unit
                  : '-'
                : ''
              this.$set(this.GeneratorSetInfo, 'Windage', WindageValue)
            } else {
              this.$set(this.GeneratorSetInfo, 'Windage', '')
            }
            this.$nextTick(() => {
              this.$emit('changeGeneratorSetInfo', this.GeneratorSetInfo)
            })
            // 保存监控模块数值
            let data = {}
            AllDataFieldMatch.forEach((item) => {
              if (nowdata[item.prop]) {
                data[item.prop] =
                  nowdata[item.prop].Value != '####' ? nowdata[item.prop] : { Value: '-', Unit: '' }
              } else {
                // 未包含相关传感器单元 设置为空
                data[item.prop] = { Value: '-', Unit: '' }
              }
            })
            // console.log('realTimeData', data)
            // this.realTimeData = data
            this.$set(this, 'realTimeData', data)

            // 保存控制开关状态
            AllControlSwitchData.forEach((item) => {
              if (nowdata[item.props]) {
                this.$set(
                  this.switchStatus,
                  item.key + '_flag',
                  nowdata[item.props].Value == 1 ? true : false
                )
              }
            })

            // 图例实时刷新
            this.$nextTick(() => {
              // 如果是第一次进入页面，判断控制单元显隐
              if (this.IsFirstRequst) {
                this.IsFirstRequst = false

                const RenderKeys = Object.keys(this.realTimeData)
                // 图例初始化
                this.echart_arr.forEach((item, index) => {
                  if (RenderKeys.includes(item.key)) {
                    item.IsRender = true
                    return
                  }
                  item.IsRender = false
                })
                this.$nextTick(() => {
                  this.init()
                  this.loading = false
                })
                return
              }

              this.refreshData()
              this.loading = false
            })
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },

    init() {
      // 图例初始化
      this.echart_arr.forEach((item) => {
        if (item.IsRender) {
          const chartDom = document.getElementById(item.id)
          const myChart = this.$echarts.init(chartDom)
          let option = null
          if (item.seriesType === 'gauge') {
            option = this.getGaugeOption(item)
          }
          if (item.seriesType === 'liquidFill') {
            option = this.getLiquidFillOption(item)
          }
          myChart.setOption(option)
          this.myCharts.push(myChart)
        }
      })
      // console.log(this.myCharts)
    },

    refreshData() {
      // 图例刷新
      this.echart_arr.forEach((item, index) => {
        if (item.IsRender) {
          let option = null
          if (item.seriesType === 'gauge') {
            option = this.getGaugeOption(item)
          }
          if (item.seriesType === 'liquidFill') {
            option = this.getLiquidFillOption(item)
          }
          if (this.myCharts[index]) {
            this.myCharts[index].setOption(option)
          }
        }
      })
    },

    // 设置仪表盘配置项
    getGaugeOption(props) {
      const value = this.realTimeData[props.key].Value
      return {
        series: [
          {
            type: 'gauge',
            radius: '85%',
            min: props.min,
            max: props.max,
            splitNumber: 4,
            axisLine: {
              lineStyle: {
                width: 6,
                color: props.axisLineSetting || [
                  [0.05, '#555555'],
                  [0.1, '#d9001b'],
                  [0.6, '#f59a23'],
                  [1, '#228b22']
                ]
              }
            },
            pointer: {
              icon: 'path://M2090.36389,615.30999 L2090.36389,615.30999 C2091.48372,615.30999 2092.40383,616.194028 2092.44859,617.312956 L2096.90698,728.755929 C2097.05155,732.369577 2094.2393,735.416212 2090.62566,735.56078 C2090.53845,735.564269 2090.45117,735.566014 2090.36389,735.566014 L2090.36389,735.566014 C2086.74736,735.566014 2083.81557,732.63423 2083.81557,729.017692 C2083.81557,728.930412 2083.81732,728.84314 2083.82081,728.755929 L2088.2792,617.312956 C2088.32396,616.194028 2089.24407,615.30999 2090.36389,615.30999 Z',
              length: '75%',
              width: 6,
              offsetCenter: [0, '5%'],
              itemStyle: {
                color: value == '-' ? '#808080' : 'auto'
              }
            },
            axisTick: {
              show: false
            },
            splitLine: {
              distance: -4,
              length: 12,
              lineStyle: {
                color: 'auto',
                width: 1
              }
            },
            axisLabel: {
              color: '#0099FF',
              distance: 10,
              fontSize: 12,
              formatter: function (v) {
                return v.toFixed(0)
              }
            },
            detail: {
              show: false
            },
            data: [
              {
                value: value || props.value
              }
            ]
          }
        ]
      }
    },

    // 设置水球图配置项
    getLiquidFillOption(props, value) {
      const percentData = `${this.realTimeData[props.key].Value} ${
        this.realTimeData[props.key].Unit
      }`
      const percentValue = this.realTimeData[props.key].Value / 100
      const realData = this.realTimeData[props.realKey]
      let realValue = `${realData.Value} ${realData.Unit}` || props.value
      const option = {
        series: [
          {
            type: 'liquidFill',
            radius: '80%',
            data: [percentValue],
            amplitude: '3%',
            waveLength: '25%',
            outline: {
              show: true,
              borderDistance: 2,
              itemStyle: {
                color: 'none',
                borderColor: '#294D99',
                borderWidth: 2,
                shadowBlur: 10,
                shadowColor: 'rgba(0, 0, 0, 0.25)'
              }
            },
            label: {
              formatter: `${realValue} \n\n ${percentData}`, // \n ${props.des}
              fontSize: 12
            }
          }
        ]
      }
      return option
    },

    // 点击控制区开关
    handleInput(key, event) {
      this.switchStatus[`${key}_flag`] = !event
    },

    handleClickSwitch(key, val) {
      const rowData = AllControlSwitchData.find((item) => item.key == key)
      const activeName = this.switchStatus[`${key}_flag`] ? '关闭' : '开启'
      const title = `${activeName}${rowData.name}`
      const content = `确定${activeName}${rowData.name}吗？`
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: title,
        message: h('div', [h('p', [h('span', content)])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const code = this.switchStatus[`${key}_flag`] ? rowData.closeCode : rowData.openCode
          this.changeSwitchStatus(key, code)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    changeSwitchStatus(key, code) {
      const query = { id: this.selectCrew.ID, key: code }
      this.$api.operation
        .control({}, query)
        .then((res) => {
          this.$message.success(res.Message)
          this.switchStatus[`${key}_flag`] = !this.switchStatus[`${key}_flag`]
          clearInterval(this.animation)
          this.initInterval()
        })
        .catch((error) => {
          this.$message.error(error)
        })
    },

    formatDate(val) {
      return formatTime(val).format('yyyy-MM-DD')
    },

    /**
     * 维保策略详情
     */
    handleShowDetail() {
      this.configData = {
        activeTab: 14,
        gensetId: this.selectCrew.ID,
        type: 1,
        runStatus: true
      }
      this.showDetailDialigVisible = true
    },
    /**
     * 维保策略优化
     */
    handleEditMaintenanceClick() {
      // 查询维保策略详情
      this.$api.project
        .GetMaintenanceGensetSettingDetail({}, { GensetId: this.selectCrew.ID })
        .then((res) => {
          if (res.Code === 1) {
            this.$set(this, 'GensetSettingDetail', res.Data)
            this.editDialigVisible = true
          } else {
            this.$message({
              type: 'error',
              message: '机组维保策略查询失败！'
            })
          }
        })
    },

    handleAddClick() {
      this.$refs.editTable.handleAdd()
    },

    tableDataChange(val) {
      this.$set(this.GensetSettingDetail, 'ProgramList', val.data)
    },
    addDataItem() {
      const domWrapper = document.querySelector('.edit-maintenance-dialog .el-dialog__body')
      const currentScroll = domWrapper.scrollTop // 已经被卷掉的高度
      const clientHeight = domWrapper.offsetHeight - 39 // 容器高度
      const scrollHeight = domWrapper.scrollHeight - 39 // 内容总高度
      // console.log(domWrapper, currentScroll, clientHeight, scrollHeight)

      domWrapper.scrollTo(0, scrollHeight - clientHeight)
    },
    // 保存
    handleSave() {
      if (
        !this.GensetSettingDetail.ProgramList ||
        (this.GensetSettingDetail.ProgramList && this.GensetSettingDetail.ProgramList.length == 0)
      ) {
        this.$message({
          type: 'warning',
          message: '请添加维保事项!'
        })
        return
      }
      // 编辑策略
      const ItemList = this.GensetSettingDetail.ProgramList.map((item) => {
        return {
          MaintenanceProgramId: item.MaintenanceProgramId,
          MaintenanceItemId: item.MaintenanceItemId,
          ItemContent: item.ItemContent,
          DefaultHourSetting: item.DefaultHourSetting,
          DefaultDaySetting: item.DefaultDaySetting
        }
      })
      const params = {
        StrategyId: this.GensetSettingDetail.StrategyId,
        ProgramList: ItemList
      }
      const query = { GensetId: this.selectCrew.ID }
      this.$api.screenData.EditMaintenanceGensetSettingV2(params, query).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '编辑机组维保策略成功!'
          })
          this.closeDialog('editDialigVisible', { refresh: true })
        }
      })
    },

    /**
     * 更多维保事项
     */
    handleShowMoreClick() {
      this.matterListDialigVisible = true
    },

    /**
     * 维保记录弹窗
     */
    handleShowRecordsClick() {
      this.showRecordsDialigVisible = true
    },

    // 重置筛选条件
    reset() {
      this.$set(this, 'searchForm', {
        searchTime: null,
        keyWord: ''
      })
    },
    // 记录查询
    searchData() {
      console.log(this.searchForm)
      return
      // const params = {
      //   ...this.searchForm
      // }
      // this.$api.operation
      //   .RemoteLockGenSet(params)
      //   .then((res) => {
      //     if (res.Code === 1) {
      //       this.$message({
      //         type: 'success',
      //         message: res.Message
      //       })
      //     } else {
      //       this.$message({
      //         type: 'error',
      //         message: res.Message
      //       })
      //     }
      //     this.closeDialog('showRecordsDialigVisible', { refresh: true })
      //   })
      //   .catch(() => {})
    },
    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.initData()
      }
    }
  },
  beforeDestroy() {
    clearInterval(this.animation)
    window.removeEventListener('resize', this.resizefun)
    this.resizefun = null
  }
}
</script>

<style lang="less" scoped>
#sensor-monitor {
  flex: 1;
  .sensor-monitor-container {
    height: 100%;
    .maintenance-items {
      height: 100%;
      &:nth-child(1),
      &:nth-child(2) {
        margin-right: 16px;
      }
    }
    .monitor-items-area {
      display: flex;
      flex-direction: column;
      padding: 8px;
      border: 1px solid hsla(0, 0%, 100%, 0.3);
      &.maintenance-info,
      &.fuel-unit,
      &.engineoil-unit {
        margin-bottom: 16px;
      }
      .area-top {
        .items-title {
          font-weight: 700;
          font-size: 16px;
          line-height: 32px;
          i {
            cursor: pointer;
          }
        }
        .items-btns {
          display: flex;
          justify-content: end;
        }
      }
      /deep/.el-switch {
        height: 32px;
        .el-switch__core {
          width: 72px !important;
          height: 26px !important;
          border-radius: 13px;
          &:after {
            width: 22px;
            height: 22px;
          }
        }
        &.is-checked .el-switch__core::after {
          margin-left: -23px;
        }
      }
    }
    .maintenance-info {
      min-height: 310px;
      box-sizing: border-box;
      .operate-btns {
        display: flex;
        justify-content: space-between;
        font-size: 12px;
        margin-bottom: 4px;
        .el-button--text {
          font-size: 12px;
        }
      }
    }
    .maintenance-items {
      display: flex;
      flex-direction: column;
      .vidio-container,
      .cooling-water,
      .waste-liquid-tank {
        flex: 1;
      }
      .vidio-container {
        .maintenance-items-container {
          justify-content: center;
        }
      }
    }
    // 实时数据
    .fuel-unit,
    .engineoil-unit {
      height: 70%;
      box-sizing: border-box;

      .maintenance-items-container /deep/.el-row {
        height: 33.33%;
      }
    }
    .cooling-water,
    .waste-liquid-tank {
      flex: 1;
      .maintenance-items-container /deep/.el-row {
        height: 100%;
      }
      .data-items {
        display: flex;
        span {
          flex: 1;
        }
      }
    }
    // 传感器单元
    .maintenance-items-container {
      box-sizing: border-box;
      padding-top: 8px;
      position: relative;
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      > p {
        text-align: center;
        font-size: 18px;
        // color: #606266;
      }
      /deep/.el-row {
        .el-col {
          &.el-col-15 {
            padding-top: 20px;
          }
          > div {
            line-height: 32px;
            > span:not(:last-child) {
              margin-right: 8px;
            }
          }
        }
      }
      .monitor-item {
        width: 100%;
        height: 100%;
      }
    }
  }
}

// 维保事项
.maintenance-matter {
  flex: 1;
  margin: 8px 0;
  li {
    display: flex;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid hsla(0, 0%, 100%, 0.3);
    &.title {
      background: #304156 !important;
    }
    div {
      width: 33.33%;
      padding: 0 4px;
      display: flex;
      justify-content: center;
      align-items: center;
      > span {
        line-height: 20px;
      }
    }
    &.no-data p {
      flex: 1;
      text-align: center;
    }
  }
}
.show-maintenance-dialog-content {
  min-height: 240px;
}
.dialog-title {
  font-size: 18px;
}
.footer-center {
  text-align: center;
}

.edit-maintenance-dialog {
  /deep/ .el-dialog {
    max-height: 680px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    .el-dialog__body {
      flex: 1;
      overflow: auto;
      position: relative;
    }
  }
  .dialog-footer {
    .add-item {
      display: flex;
      justify-self: start;
    }
  }
}
// 文本颜色
.exceed {
  color: #ec808d;
}
.normalColor em {
  color: #228b22;
}
.warningColor em {
  color: #f59a23;
}
.attentionColor em {
  color: #d9001b;
}
.nullColor em {
  color: #808080;
}

.maintenance-record-dialog {
  .search-box {
    border: 0;
    // padding: 0;
    .search-item {
      width: 37.5%;
    }
  }
  .el-timeline {
    padding: 0 16px;
  }
}
.theme-dark {
  .maintenance-record-dialog {
    /deep/ .el-timeline {
      .el-timeline-item__timestamp {
        color: #fff;
      }
    }
  }
}
.theme-light {
  .maintenance-matter {
    li.title {
      background: #f3f3f3 !important;
    }
  }
}
</style>



