// 云消息推送类型枚举 // 1运维 2代运维
const GensetCloudMessageTypeEnum = [
  { value: 1, label: '推送消息' },
  { value: 0, label: '清除消息' }
]

// 云消息推送模板
const GensetCloudMessageMoulds = [
  { value: 0, label: '业务咨询：本机使用维护、技术业务咨询请联系xxx。' },
  { value: 1, label: '验收催款：本机已完成交付验收，按合同需支付验收款，请联系xxx。' },
  { value: 2, label: '质保催款：本机一年（/1500小时）质保期已到，按合同需缴纳质保金，请联系xxx。' },
  { value: 3, label: '催款警告：本机欠款，有停机风险，请联系xxx。' },
  { value: 4, label: '可靠性提醒：本机**部件有故障风险，请及时保养或更换。' },
  { value: 5, label: '租赁催款：按合同本机需支付租赁款，请联系xxx。' },
  { value: 6, label: '维保提醒：本机维保还有**小时到期，请及时保养或联系xxx。' },
  { value: 7, label: '正品备件采购请联系xxx。' },
  { value: 8, label: '专业油品采购请联系xxx。' },
  { value: 9, label: '正品三滤采购请联系xxx。' },
  { value: 10, label: '天气寒冷，请注意添加防冻液。' },
  { value: 11, label: '冷却水温偏高，请注意加注冷却水。' },
  { value: 12, label: '机油压力偏高，请更换机油、滤清器。' },
  { value: 13, label: '机油压力偏低，请注意添加机油。' },
  { value: 14, label: '本机起动困难，请保养起动电池。' },
  { value: 15, label: '其它。' }
]

export default {
  GensetCloudMessageTypeEnum,
  GensetCloudMessageMoulds,
  // 云消息推送历史列表
  leaseGensetColumns: (that) => [
    {
      label: '设备名称',
      prop: 'GenDisplayName',
      minWidth: '20%'
    },
    {
      label: '轮播内容',
      prop: 'Values',
      minWidth: '30%'
    },
    {
      label: '轮播时长',
      prop: 'SaveTimes',
      minWidth: '15%',
      unit: 'h',
      formatter: (row, column, index) => {
        return row.SaveTimes || 0
      }
    },
    {
      label: '推送时间',
      prop: 'Create_date',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.Create_date ? that.$moment(row.Create_date).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '类型',
      prop: 'Type',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return GensetCloudMessageTypeEnum.find((item) => item.value == row.Type).label
      }
    }
  ]
}



