// 解锁机类型枚举 // 12锁机13停机 2警告 3报警 4运行
const GensetLockTypeEnum = [
  { value: 12, label: '锁机' },
  { value: 13, label: '解机' }
]
export default {
  GensetLockTypeEnum,
  // 解锁机记录列表
  leaseGensetColumns: (that) => [
    {
      label: '设备名称',
      prop: 'GeneratorName',
      minWidth: '30%'
    },
    {
      label: '类型',
      prop: 'Type',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return GensetLockTypeEnum.find((item) => item.value == row.Type).label
      }
    },
    {
      label: '操作时间',
      prop: 'Creare_Time',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.Creare_Time ? that.$moment(row.Creare_Time).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '操作人',
      prop: 'UserName',
      minWidth: '20%'
    },
    {
      label: '内容',
      prop: 'Content',
      minWidth: '25%',
      render: (h, parmas) => {
        return h(
          'span',
          {
            class: 'text-overflow-ellipsis MulLineHiding', //用类名控制文字溢出显示省略号
            attrs: {
              title: parmas.row.Content || ''
            }
          },
          parmas.row.Content
        )
      }
    }
  ]
}



