<template>
  <div>
    <OperationReportComponent ref="OperationReportComponent" :title="title" />
  </div>
</template>

<script>
import OperationReportComponent from './OperationReportComponent'
export default {
  name: 'OperationReport',
  components: {
    OperationReportComponent
  },
  data() {
    return {
      title: '机组运行报告'
    }
  },
  mounted() {},
  methods: {
    initData() {
      this.$refs.OperationReportComponent.getData()
    }
  }
}
</script>

<style lang="less" scoped></style>



