 * @Author: linxu
 * @Date: 2021-11-19 10:57:34
 * @LastEditors: linxu
 * @LastEditTime: 2021-12-28 15:39:12
 * @Description: iframe组件
-->
<template>
  <div class="frame"
       v-loading='loading'>
    <iframe id="pageIframe"
            ref="pageIframe"
            :src="src"
            marginwidth='0'
            marginheight='0'
            frameborder="0"></iframe>
  </div>
  <!--  @load="setIframeHeight(this)" -->
</template>
<script>
import ToggleCrew from '@/components/toggleCrew'
import { mapGetters } from 'vuex'

export default {
  name: 'pageIframe',
  components: {
    ToggleCrew
  },
  props: ['linkUrl'],
  data () {
    return {
      loading: true,
      iframeWin: {},
      flag: false,
      src: '',
      timeInterval: null
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  mounted () {
    this.src = this.linkUrl + this.selectCrew.ID
    this.setIframeHeight()
    const _self = this
    if (this.$refs.pageIframe.attachEvent) {
      this.$refs.pageIframe.attachEvent('onload', function () {
        // console.log(_self.loading)
        _self.loading = false
      })
    } else {
      this.$refs.pageIframe.onload = function () {
        // console.log(_self.loading)
        _self.loading = false
      }
    }
  },
  methods: {
    loaded () {
      // const cookie = document.cookie
      // this.iframeWin.postMessage(cookie, this.src)
    },
    setIframeHeight () {
      this.iframeWin = this.$refs.pageIframe.contentWindow || this.$refs.pageIframe.contentDocument.parentWindow
      if (this.iframeWin.document.body) {
        this.$refs.pageIframe.height = Math.floor(this.iframeWin.document.documentElement.scrollHeight || this.iframeWin.document.body.scrollHeight) + 1 + 'px'
      }
    }
  },
  watch: {
    // $route: {
    //   handler: function(val, oldVal){
    //     this.src = this.linkUrl + this.selectCrew.ID;
    //   },
    //   // 深度观察监听
    //   deep: true
    // },
    selectCrew (newVal, oldVal) {
      this.loading = true
      this.src = this.linkUrl + newVal.ID
    },
    linkUrl (newVal, oldVal) {
      this.loading = true
      this.src = this.linkUrl + newVal.ID
    }
  }
}
</script>

<style lang="less" scoped>
#pageIframe {
  width: 100%;
  height: 889px;
  overflow-y: hidden;
}
</style>



