<template>
  <div class="curve-container" :id="'curve-container' + id">
    <el-row class="curve-container-query" type="flex" justify="end">
      <el-button :class="{ active: quick === 1 }" type="text" @click="quick = 1">今日</el-button
      >&nbsp;
      <el-button :class="{ active: quick === 2 }" type="text" @click="quick = 2">本周</el-button
      >&nbsp;
      <el-button :class="{ active: quick === 3 }" type="text" @click="quick = 3">本月</el-button
      >&nbsp;
      <el-button :class="{ active: quick === 4 }" type="text" @click="quick = 4">本年</el-button
      >&nbsp; &nbsp;&nbsp;
      <el-date-picker
        v-model="dateRange"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        placement="bottom-start"
      >
      </el-date-picker>
    </el-row>
    <el-row class="curve-container-curve" :id="'curve-' + id"></el-row>
  </div>
</template>

<script>
import _ from 'lodash'
import dayjs from 'dayjs'
import { mapGetters } from 'vuex'

import _data from './data'
import { formatTimeDayjs } from '@/utils/common'

export default {
  name: 'Curve',
  components: {},
  data() {
    return {
      quick: 0,
      quickFlag: true,
      dateRange: [dayjs().add(-2, 'M'), dayjs().add(1, 'd')],
      curveData: {},
      curveOption: {},
      eChartInstance: null
    }
  },
  props: ['api', 'id', 'tab'],
  //   inject: [''],
  //   provide() {
  //     return {}
  //   },
  computed: {
    ...mapGetters(['theme', 'selectCrew'])
  },
  watch: {
    quick(nv) {
      this.quickFlag = true
      switch (nv) {
        case 1:
          this.dateRange = [dayjs(), dayjs()]
          break
        case 2:
          this.dateRange = [dayjs().startOf('w').add(1, 'd'), dayjs().endOf('w').add(1, 'd')]
          break
        case 3:
          this.dateRange = [dayjs().startOf('M'), dayjs().endOf('M')]
          break
        case 4:
          this.dateRange = [dayjs().startOf('y'), dayjs().endOf('y')]
          break
        default:
          break
      }
    },
    theme() {
      this.getCurveData()
    },
    selectCrew() {
      this.getCurveData()
    },
    dateRange: {
      handler: function () {
        this.quickFlag || (this.quick = 0)
        this.getCurveData()
        this.quickFlag = false
      },
      immediate: true
    },
    tab() {
      this.getCurveData()
    }
  },
  methods: {
    getCurveData() {
      const dateStart = dayjs(this.dateRange[0]).format('YYYY-MM-DD')
      const dateEnd = dayjs(this.dateRange[1]).format('YYYY-MM-DD')
      // 仅查询当前标签页的数据
      this.tab.toString() === this.id.toString() &&
        this.$api.operation[this.api]({
          // GeneratorSetId: 4125,
          GeneratorSetId: this.selectCrew.ID,
          DateStart: dateStart,
          DateEnd: dateEnd
        }).then((value) => {
          console.log(this.api, value)
          // value = _data
          const data = value.Data
          if (data) {
            const series = this.handleCurveData(data)
            const seriesCommonOption = {
              type: 'line',
              smooth: true,
              symbol: 'circle',
              showSymbol: false,
              symbolSize: 8
            }
            this.curveOption = {
              xAxis: {
                type: 'time',
                axisLine: {
                  lineStyle: {
                    color: this.theme === 'dark' ? 'white' : 'gray'
                  }
                }
              },
              yAxis: {
                type: 'value',
                axisLine: {
                  show: true,
                  lineStyle: {
                    color: this.theme === 'dark' ? 'white' : 'gray'
                  }
                },
                splitLine: {
                  lineStyle: {
                    color: '#ced6e088'
                  }
                }
              },
              tooltip: {
                trigger: 'axis',
                axisPointer: {
                  lineStyle: {
                    width: 2,
                    type: 'line'
                  }
                },
                position: function (pt) {
                  return [pt[0], '5%']
                }
              },
              dataZoom: [
                {
                  type: 'inside',
                  start: 0,
                  start: 0,
                  end: 100,
                  minSpan: 10,
                  backgroundColor: 'rgba(47,69,84,0)'
                  // 解决大数据加载卡顿问题
                  // realtime: false,
                  // throttle: 0
                },
                {
                  // start: startDataZoom,
                  // end: endDataZoom,
                  // 解决大数据加载卡顿问题
                  // realtime: false,
                  // throttle: 0,
                  start: 0,
                  end: 100,
                  minSpan: 10,
                  dataBackground: {
                    // 数据阴影的样式。
                    lineStyle: {
                      opacity: 0
                    },
                    areaStyle: {
                      opacity: 0
                    }
                  },
                  // 隐藏数据阴影
                  selectedDataBackground: {
                    lineStyle: {
                      opacity: 0
                    },
                    areaStyle: {
                      opacity: 0
                    }
                  }
                }
              ],
              series: [
                {
                  name: '诊断指标',
                  data: series.data_FNor,
                  ...seriesCommonOption,
                  markLine: {
                    symbol: 'none',
                    data: [
                      {
                        yAxis: 5,
                        lineStyle: {
                          color: '#e5b523',
                          width: '2',
                          type: 'dotted'
                        },
                        label: {
                          show: true,
                          formatter: '劣化指标：5',
                          color: '#e5b523'
                        }
                      },
                      {
                        yAxis: 2.576,
                        lineStyle: {
                          color: '#b34c52',
                          width: '2',
                          type: 'dotted'
                        },
                        label: {
                          show: true,
                          formatter: '诊断指标：2.576',
                          color: '#b34c52'
                        }
                      }
                    ]
                  }
                },
                {
                  name: '劣化指标',
                  data: series.data_FTmp,
                  type: 'line',
                  ...seriesCommonOption
                }
              ]
            }
            this.eChartInstance ||
              (this.eChartInstance = this.$echarts.init(
                document.getElementById('curve-' + this.id)
              ))
            this.eChartInstance.setOption(this.curveOption)
            this.eChartInstance.resize()
          }
        })
    },
    handleCurveData(data) {
      const data_FNor = []
      const data_FTmp = []
      // 时间轴去重
      const timeSet = new Set()
      data.forEach((e) => {
        if (timeSet.has(e.OperateDate)) {
          return
        }
        const time = formatTimeDayjs(e.OperateDate).format('YYYY-MM-DD HH:mm:ss')
        data_FNor.push([time, Number(e.FNor)])
        data_FTmp.push([time, Number(e.FTmp)])
        timeSet.add(e.OperateDate)
      })
      return {
        data_FNor,
        data_FTmp
      }
    },
    handleEChartResize: _.debounce(function () {
      this.eChartInstance.resize()
    }, 300)
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    window.addEventListener('resize', this.handleEChartResize)
  },
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {
    window.removeEventListener('resize', this.handleEChartResize)
  },
  destroyed() {}
}
</script>
<style lang="less" scoped>
.curve-container {
  width: 100%;
  height: 100%;
  &-query {
    .el-button {
      color: rgb(163, 155, 155) !important;
    }
    .active {
      color: #4dc9b0 !important;
    }
  }
  &-curve {
    height: 500px;
  }
}
</style>



