<!--
 * @Autor: linxu
 * @Date: 2022-02-15 15:50:34
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 14:00:24
 * @Description: 机组出厂实验报告详情
-->
<template>
  <div>
    <!-- <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    /> -->
    <div class="delivery-test-report-detail">
      <div class="operate-box">
        <div>
          <el-button
            type="primary"
            icon="el-icon-arrow-left"
            @click="() => this.$emit('handleBack')"
            >返回列表
          </el-button>
          <el-button type="primary" icon="el-icon-download" @click="downloadFile">下载</el-button>
        </div>
        <div class="data-source">
          数据来源：<span>机组信息获取</span><span>手动输入</span><span>云数据获取</span>
        </div>
      </div>
      <div id="descriptions-box" class="descriptions-box">
        <!-- 发电机组试验报告 -->
        <p class="part-title bold">发电机组{{ recordInfo.Trigger || '试验' }}报告</p>
        <el-descriptions class="descriptions_part_1" title="" :column="5" border>
          <template v-for="(item, index) in descriptionsItems1">
            <el-descriptions-item :key="index" :span="item['size']">
              <template slot="label">{{ item['label'] }}</template>
              <div class="description-content-box">
                <!-- <el-input v-model="dataSource[item['key']]"
                        placeholder=""></el-input> -->
                <span>{{ dataSource[item['key']] }}</span>
                <span v-if="item['unit']">{{ item['unit'] }}</span>
              </div>
            </el-descriptions-item>
          </template>
        </el-descriptions>

        <!-- 测试项目 -->
        <p class="part-title">测试项目</p>
        <div class="test-project">
          <el-row class="test_part test_part_1">
            <el-col :span="4">
              <p>外观检测</p>
            </el-col>
            <el-col :span="2">
              <p class="text-center"></p>
            </el-col>
            <el-col :span="6">
              <p class="text-center">绝缘介质强度测试</p>
            </el-col>
            <el-col :span="2">
              <p class="text-center"></p>
            </el-col>
            <el-col :span="10">
              <p class="text-center">检查控制屏上各工作状态指示灯</p>
            </el-col>
          </el-row>
          <el-row class="test_part test_part_2">
            <el-col :span="4">
              <p>绝缘测量</p>
              <p>电枢对地</p>
              <p>绕组对地</p>
            </el-col>
            <el-col :span="2">
              <p class="line3 text-center">
                <span></span>
              </p>
            </el-col>
            <el-col :span="6">
              <p class="text-center">常温下自启动功能(启动三次)</p>
              <p class="text-center">检查相序</p>
              <p class="text-center">检查空载电压调压范围:76.5%-124.5%</p>
            </el-col>
            <el-col :span="2">
              <p class="text-center"></p>
              <p class="text-center"></p>
              <p class="text-center"></p>
            </el-col>
            <el-col :span="5">
              <p>检查指示仪表</p>
              <p>检查高水温保护</p>
              <p>检查低油压保护</p>
            </el-col>
            <el-col :span="5">
              <p>检查超速停机</p>
              <p>检查紧急停机</p>
              <p>检查蓄电池充电情况</p>
            </el-col>
          </el-row>
        </div>

        <!-- 稳态测量及电压、频率的瞬态测量 -->
        <p class="part-title text-left">稳态测量及电压、频率的瞬态测量</p>
        <el-table class="test-table" :data="tableData" style="width: 100%">
          <el-table-column prop="loadPercentage" label="负载百分比" min-width="5%">
          </el-table-column>
          <el-table-column prop="power" label="KW" min-width="10%"> </el-table-column>
          <el-table-column label="电压(V)">
            <el-table-column prop="voltageA" label="UA" min-width="5%"> </el-table-column>
            <el-table-column prop="voltageB" label="UB" min-width="5%"> </el-table-column>
            <el-table-column prop="voltageC" label="UC" min-width="5%"> </el-table-column>
          </el-table-column>
          <el-table-column label="电流(A)">
            <el-table-column prop="currentA" label="IA" min-width="5%"> </el-table-column>
            <el-table-column prop="currentB" label="IB" min-width="5%"> </el-table-column>
            <el-table-column prop="currentC" label="IC" min-width="5%"> </el-table-column>
          </el-table-column>
          <el-table-column prop="powerFactor" label="功率因数" min-width="5%"> </el-table-column>
          <el-table-column prop="frequency" label="频率(Hz)" min-width="5%"> </el-table-column>
          <el-table-column prop="voltageError" label="电压稳态误差(%)" min-width="5%">
          </el-table-column>
          <el-table-column prop="frequencyError" label="频率稳态误差(%)" min-width="5%">
          </el-table-column>
          <el-table-column label="一分钟B相电压">
            <el-table-column prop="voltageBMax" label="最大" min-width="5%"> </el-table-column>
            <el-table-column prop="voltageBMin" label="最小" min-width="5%"> </el-table-column>
          </el-table-column>
          <el-table-column label="一分钟B相频率">
            <el-table-column prop="currentBMax" label="最大" min-width="5%"> </el-table-column>
            <el-table-column prop="currentBMin" label="最小" min-width="5%"> </el-table-column>
          </el-table-column>
          <el-table-column prop="voltageVolatility" label="电压波动率(%)" min-width="5%">
          </el-table-column>
          <el-table-column prop="frequencyVolatility" label="频率波动率(%)" min-width="5%">
          </el-table-column>
          <el-table-column label="备注" min-width="5%">
            <template>
              <el-button type="text">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <p class="part-title">
          <el-button type="text">添加</el-button>
        </p>
        <el-row class="test_part test_part_3">
          <el-col :span="2">
            <p class="line2 text-center">测试结果</p>
          </el-col>
          <el-col :span="5">
            <p>电压稳态误差(%)</p>
            <p>电压波动率(%)</p>
          </el-col>
          <el-col :span="5">
            <p>{{ voltageError }}</p>
            <p>{{ voltageVolatility }}</p>
          </el-col>
          <el-col :span="8">
            <p>频率稳态误差(%)</p>
            <p>频率波动率(%)</p>
          </el-col>
          <el-col :span="4">
            <p>{{ frequencyError }}</p>
            <p>{{ frequencyVolatility }}</p>
          </el-col>
        </el-row>
        <el-row class="test_part test_part_3">
          <el-col :span="2">
            <p class="line2 text-center"><span>测试结果</span></p>
          </el-col>
          <el-col :span="22">
            <el-row>
              <p></p>
              <div class="no-padding">
                <el-row class="test_part__row">
                  <el-col :span="2">
                    <p>测试人员</p>
                  </el-col>
                  <el-col :span="4">
                    <p>{{ testPerson }}</p>
                  </el-col>
                  <el-col :span="2">
                    <p>检验员</p>
                  </el-col>
                  <el-col :span="4">
                    <p>{{ surveyor }}</p>
                  </el-col>
                  <el-col :span="2">
                    <p>测试日期</p>
                  </el-col>
                  <el-col :span="4">
                    <p>{{ testDate }}</p>
                  </el-col>
                  <el-col :span="2">
                    <p>测试时间</p>
                  </el-col>
                  <el-col :span="4">
                    <p>{{ testTime }}</p>
                  </el-col>
                </el-row>
              </div>
            </el-row>
          </el-col>
        </el-row>

        <p class="part-title" style="height: 200px"></p>
        <!-- 曲线组件 -->
        <DeliveryTestReportDetailCurve :data="dataSource" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { htmlToPdf } from '@/utils/util'
import DeliveryTestReportDetailCurve from './DeliveryTestReportDetailCurve'
const descriptionsItems1 = [
  {
    label: '发电机组型号',
    key: '发电机组型号',
    unit: '',
    size: ''
  },
  {
    label: '原动力型号',
    key: '原动力型号',
    unit: '',
    size: ''
  },
  {
    label: '发动机型号',
    key: '发动机型号',
    unit: '',
    size: ''
  },
  {
    label: '电机型号',
    key: '电机型号',
    unit: '',
    size: ''
  },
  {
    label: '控制器型号',
    key: '控制器型号',
    unit: '',
    size: ''
  },
  {
    label: '发电机组序列号',
    key: '发电机组序列号',
    unit: '',
    size: ''
  },
  {
    label: '执行器型号',
    key: '执行器型号',
    unit: '',
    size: ''
  },
  {
    label: '发动机序列号',
    key: '发动机序列号',
    unit: '',
    size: ''
  },
  {
    label: '电机序列号',
    key: '电机序列号',
    unit: '',
    size: ''
  },
  {
    label: 'AVR型号',
    key: 'AVR型号',
    unit: '',
    size: ''
  },
  {
    label: '视在功率',
    key: '发电机组视在功率',
    unit: 'kVA',
    size: ''
  },
  {
    label: '功率因数',
    key: '发电机组功率因数',
    unit: '',
    size: ''
  },
  {
    label: '额定频率',
    key: '发电机组额定频率',
    unit: 'Hz',
    size: ''
  },
  {
    label: '额定转速',
    key: '发电机组额定转速',
    unit: 'r/min',
    size: '2'
  },
  {
    label: '额定功率',
    key: '发电机组额定功率',
    unit: 'kW',
    size: ''
  },
  {
    label: '额定电压',
    key: '发电机组额定电压',
    unit: 'V',
    size: ''
  },
  {
    label: '额定电流',
    key: '发电机组额定电流',
    unit: 'A',
    size: ''
  },
  {
    label: '励磁方式',
    key: '',
    unit: '',
    size: '2'
  },
  {
    label: '相对湿度',
    key: '相对湿度',
    unit: '',
    size: ''
  },
  {
    label: '环境温度',
    key: '环境温度',
    unit: '',
    size: '1.5'
  },
  {
    label: '大气压力',
    key: '大气压力',
    unit: '',
    size: ''
  },
  {
    label: '海拔高度',
    key: '海拔',
    unit: 'm',
    size: ''
  }
]

export default {
  name: 'DeliveryTestReportDetail',
  components: {
    DeliveryTestReportDetailCurve
  },
  props: ['recordInfo'],
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.handleBackClick()
      },
      // 深度观察监听
      deep: true
    }
  },
  data() {
    return {
      // recordInfo: JSON.parse(this.$route.query.record),

      descriptionsItems1, // 发电机组试验报告 描述项
      dataSource: {}, // 发电机组试验报告数据

      // 负载百分比
      loadPercentageList: [
        '0%',
        '25%',
        '50%',
        '75%',
        '100%',
        '110%',
        '100%',
        '75%',
        '50%',
        '25%',
        '0%'
      ],
      tableData: null,

      voltageError: '',
      frequencyError: '',
      voltageVolatility: '',
      frequencyVolatility: '',
      testPerson: '',
      surveyor: '',
      testDate: '',
      testTime: ''
    }
  },
  mounted() {
    this.getCurveData()
    console.log('mounted----recordInfo', this.recordInfo)
  },
  methods: {
    // 获取曲线详情数据
    async getCurveData() {
      // 取并集
      const _this = this
      const params = {
        ...this.recordInfo
      }
      // 获取各项目数据 和 数据精度
      let Precisions = null
      let data = await this.$api.equipment.GetGenRecordCurves(params).then((res) => {
        if (res.Code === 1) {
          return res.Data
        }
      })
      console.log(data, Precisions)

      // 记录表数据赋值
      // this.dataSource = data
      this.$set(this, 'dataSource', data)
      // 稳态测量
      let table = [],
        voltageErrorArr = [], // 电压稳态误差
        frequencyErrorArr = [], // 频率稳态误差
        voltageVolatilityArr = [], // 电压波动率
        frequencyVolatilityArr = [] // 频率波动率
      this.loadPercentageList.forEach((item, index) => {
        let tableItem = {
          loadPercentage: item,
          power: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-功率`] / 10,
          voltageA: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-电压UA`] / 10,
          voltageB: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-电压UB`] / 10,
          voltageC: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-电压UC`] / 10,
          currentA: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-电流IA`] / 10,
          currentB: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-电流IB`] / 10,
          currentC: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-电流IC`] / 10,
          powerFactor: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-功率因数`] / 1000,
          frequency: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-频率`] / 100,
          voltageError: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-电压稳态误差`] / 100,
          frequencyError: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-频率稳态误差`] / 100,
          voltageBMax: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-B相电压最大`] / 10,
          voltageBMin: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-B相电压最小`] / 10,
          currentBMax: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-B相频率最大`] / 100,
          currentBMin: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-B相频率最小`] / 100,
          voltageVolatility: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-电压波动率`] / 100,
          frequencyVolatility: data[`(${item})稳态测量${index <= 5 ? '' : '(降)'}-频率波动率`] / 100
        }
        table.push({
          ...tableItem,
          voltageVolatility: '±' + tableItem.voltageVolatility,
          frequencyVolatility: '±' + tableItem.frequencyVolatility
        })
        voltageErrorArr.push(tableItem.voltageError)
        frequencyErrorArr.push(tableItem.frequencyError)
        voltageVolatilityArr.push(tableItem.voltageVolatility)
        frequencyVolatilityArr.push(tableItem.frequencyVolatility)
      })
      console.log(table)
      this.$set(this, 'tableData', table)
      this.voltageError = this.arrAverageNum2(voltageErrorArr)
      this.frequencyError = this.arrAverageNum2(frequencyErrorArr)
      this.voltageVolatility = '±' + this.arrMaxNum2(voltageVolatilityArr)
      this.frequencyVolatility = '±' + this.arrMaxNum2(frequencyVolatilityArr)

      this.testDate = data['测试日期']
      this.testTime = data['测试时间']
    },
    arrMaxNum2(arr) {
      return Math.max.apply(null, arr)
    },
    arrMinNum2(arr) {
      return Math.min.apply(null, arr)
    },
    arrAverageNum2(arr) {
      var sum = eval(arr.join('+'))
      return ~~((sum / arr.length) * 100) / 100
    },
    handleBackClick() {
      this.$router.push({
        path: '/equipment/crewreport-detail',
        query: { selectTab: this.$route.query.selectTab || 0, pageFlag: 1, active: 8 }
      })
    },
    // 下载
    downloadFile() {
      const props = {
        title: `发电机组${this.recordInfo.Trigger || '试验'}报告`,
        contentId: '#descriptions-box'
      }
      htmlToPdf(props)
    }
  }
}
</script>

<style lang="less" scoped>
.operate-box {
  margin: 16px 0;
  display: flex;
  justify-content: space-between;
  .data-source {
    font-weight: bold;
    span {
      display: inline-block;
      background-color: #f2f2f2;
      padding: 5px 12px;
      margin-left: 4px;
      border-radius: 5px;
      color: #373a3c;
    }
  }
}
@fontColor: {
  color: #000;
};

/deep/.descriptions-box p.part-title {
  @borderBottom();
  line-height: 32px;
  text-align: center;
  padding: 4px;
  box-sizing: border-box;
  &.bold {
    line-height: 40px;
    font-weight: bold;
    font-size: 22px;
  }
  &.text-left {
    text-align: left;
  }
}
.descriptions-box {
  border: 1px solid @borderColor;
  /deep/.el-descriptions.descriptions_part_1 {
    .el-descriptions-item__cell {
      width: 200px;
      padding: 10px 8px;
      // @fontColor();
      &.el-descriptions-item__label.is-bordered-label {
        text-align: center;
      }
    }
  }
  .description-content-box {
    display: flex;
    align-items: center;
    /deep/.el-input {
      .el-input__inner {
        border: 0;
        padding: 0;
      }
    }
    > span {
      &:first-child {
        flex: 1;
      }
      margin-left: 2px;
    }
  }
}

// 测试项目
@borderColor: #ebeef5;
@borderTop: {
  border-top: 1px solid @borderColor;
};
@borderRight: {
  border-right: 1px solid @borderColor;
};
@borderBottom: {
  border-bottom: 1px solid @borderColor;
};
@borderLeft: {
  border-left: 1px solid @borderColor;
};
/deep/.el-row.test_part {
  line-height: 42px;
  box-sizing: border-box;
  @borderLeft();
  // @borderTop();
  .el-col {
    @borderBottom();
    @borderRight();
    p {
      height: 42px;
      line-height: 32px;
      box-sizing: border-box;
      padding: 5px 4px;
      display: flex;
      align-items: center;
      span {
        line-height: 20px;
      }
      &.text-center {
        // text-align: center;
        justify-content: center;
      }
      &:not(:last-child) {
        @borderBottom();
      }
    }
  }
}
/deep/.el-row.test_part {
  .el-col {
    p.line2 {
      height: 84px;
      line-height: 74px;
    }
    p.line3 {
      height: 126px;
      line-height: 126px;
    }
  }
  .test_part__row {
    .el-col {
      border: 0;
      &:not(:first-child) {
        @borderLeft();
      }
    }
  }
}
/deep/.test-table.el-table {
  border-top: none;
  .cell {
    // @fontColor();
    display: flex;
    align-content: center;
    align-items: center;
    line-height: 20px;
  }
  tr.el-table__row {
    background-color: #c2c2c2 !important;
    &:hover td:not(:last-child) {
      background-color: #c2c2c2 !important;
    }
    > td.el-table__cell {
      @borderBottom();
      @borderRight();
      // cursor: pointer;
      &:last-child {
        background-color: #fff;
      }
    }
  }
}

.theme-dark #descriptions-box {
  background-color: #102447;
}
</style>



