<!--
 * @Autor: linxu
 * @Date: 2022-01-18 16:30:06
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-06 11:38:42
 * @Description: 出厂试验报告
-->
<template>
  <TableList
    :options="options"
    :columns="columns"
    :dataSource="dataSource"
    :operates="operates(this)"
    :dataTotal="dataTotal"
    :pagination="paginationCfg"
    @handleChangePage="handleChangePage"
    @handleChangeSize="handleChangeSize"
    v-loading="loading"
  />
</template>

<script>
import { mapGetters } from 'vuex'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
// 表格头设置
const columns = (that) => [
  {
    label: '索引值',
    prop: 'IdX'
  },
  {
    label: '试验类型',
    prop: 'Trigger'
  },
  {
    label: '创建时间',
    prop: 'Time',
    formatter: (row, column, index) => {
      return row.Time ? that.$moment(row.Time).format('yyyy-MM-DD HH:mm:ss') : ''
    }
  }
]
// 表格操作按钮
const operates = (that) => [
  {
    label: '查看报告',
    method: (row) => {
      that.handleShowReport(row)
    }
  },
  {
    label: '删除',
    method: (row) => {
      that.handleDelete(row)
    },
    setStyle: (row) => {
      return 'color: red;'
    }
  }
  // {
  //   label: '重新请求',
  //   method: (row) => {
  //     that.handleReRequest(row)
  //   }
  // }
]
export default {
  name: 'DeliveryTestReport',
  mixins: [paginationConfigMixin],
  data() {
    return {
      loading: false,
      columns: columns(this),
      operates,
      dataSource: [],
      options: {
        index: true,
        labelIndex: '序号'
      }
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        // console.log(newVal, oldVal)
        this.paginationCfg.currentPage = 1
        this.queryTableData()
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      this.loading = true
      const query = {
        page: this.paginationCfg.currentPage,
        pageSize: this.paginationCfg.pageSize
      }
      const params = {
        Type: '测试记录',
        Trigger: '出厂试验',
        GeneratorId: this.selectCrew.ID
      }
      this.$api.equipment
        .GeneratorSetRecordingList(params, query)
        .then((res) => {
          if (res.Code === 1) {
            this.dataTotal = res.Data.TotalItemCount
            this.$set(this, 'dataSource', res.Data.Data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 查看报告
    handleShowReport(row) {
      // console.log('handleShowReport', row)
      // this.pageFlag = 0
      const record = {
        IndexID: row.IdX,
        Type: row.Type,
        Trigger: row.Trigger,
        GeneratorSetId: row.GeneratorSetId
      }
      let text = this.$router.resolve({
        path: '/equipment/crewreport-detail',
        query: { selectTab: 0, pageFlag: 0, record: JSON.stringify(record) }
      })
      // window.open(text.href, '_self') // , '_blank'
      this.$emit('showReport', { record, selectTab: 0, pageFlag: 0 })
    },
    // handleBackClick () {
    //   // this.pageFlag = 1
    //   this.$router.push({ path: '/equipment/crewrecord/oneclick-repairrecord', query: { pageFlag: 1 } })
    // },
    // 删除
    handleDelete(row) {
      // console.log(row)
      const h = this.$createElement
      this.$confirm('删除提示', {
        title: '删除提示',
        message: h('div', [
          h('p', '此操作将导致数据无法恢复，请谨慎操作!'),
          h('p', '是否确定删除数据?')
        ]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          let query = {
            id: row.RecordingId
          }
          this.$api.equipment
            .DeleteGeneratorSetFullRecord(query)
            .then((res) => {
              if (res.Code === 1) {
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                })
                this.paginationCfg.currentPage = 1
                this.queryTableData()
              }
            })
            .catch(() => {
              this.$message({
                type: 'success',
                message: '删除失败!'
              })
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 重新请求
    handleReRequest(row) {
      // console.log(row)
      this.$confirm('是否需要重新获取数据？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$message({
            type: 'success',
            message: '重新获取数据成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    }
  }
}
</script>

<style lang="less" scoped></style>



