<!--
 * @Autor: linxu
 * @Date: 2021-12-30 13:51:33
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-25 17:18:32
 * @Description: 机组关重件
-->
<template>
  <div class="crew-key-parts">
    <div class="crewinfo-card">
      <!-- <div class="crewinfo-card-header">机组关重件</div> -->
      <div class="operate-btns">
        <el-button
          type="primary"
          class="addHistoryBtn"
          @click="handleAddKeyParts()"
        >
          添加
        </el-button>
      </div>    
      <div class="crewinfo-card-content">
        <el-table
          ref="mainTable"
          :data="tableData"
          style="width: 100%"
          border
          tooltip-effect="light"
          v-loading="loading"
        >
          <!-- 列表无数据 -->
          <template slot="empty">
            <el-empty image="/static/img/nodata.png" :image-size="80"></el-empty>
          </template>
          <el-table-column type="index" :index="handleChangeIndex" label="序号" align="center" width="60">
          </el-table-column>
          <el-table-column prop="PartNameKey" label="关重件" min-width="12%">
            <template slot-scope="scope">
              <div>{{ scope.row['PartNameKey'] }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="PartSerialNumber" label="出厂编号" min-width="12%">
            <template slot-scope="scope">
              <div>{{ scope.row['PartSerialNumber'] }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="PartBrandName" label="品牌" min-width="12%">
            <template slot-scope="scope">
              <div>{{ scope.row['PartBrandName'] }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="PartModelName" label="型号" min-width="12%">
            <template slot-scope="scope">
              <div>{{ scope.row['PartModelName'] }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="PartStandardName" label="规格" min-width="12%">
            <template slot-scope="scope">
              <div>{{ scope.row['PartStandardName'] }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="PartFactory" label="厂商" min-width="12%">
            <template slot-scope="scope">
              <div>{{ scope.row['PartFactory'] }}</div>
            </template>
          </el-table-column>
          <!-- <el-table-column label="操作"
                           width="160">
            <template slot-scope="scope">
              <div class="table-hover-show-btn">
                <el-button type="text"
                           @click="showDetail(scope.row, 0)">机组信息</el-button>
                <el-button type="text"
                           @click="showDetail(scope.row, 1)">机组报告</el-button>
              </div>
            </template>
          </el-table-column> -->
        </el-table>
        <!-- 分页器 -->
        <!-- <div class="pagination-box mt15 mb24">
          <el-pagination
            align="center"
            @size-change="handleChangeSize"
            @current-change="handleChangePage"
            :current-page="paginationCfg.currentPage"
            :page-sizes="paginationCfg.pageSizes"
            :page-size="paginationCfg.pageSize"
            :layout="paginationCfg.layout"
            :total="dataTotal"
          >
          </el-pagination>
        </div> -->
      </div>
    </div>

    <!-- 新增弹窗 -->
    <el-dialog
      title="添加机组关重件"
      class="keyparts-add-dialog"
      width="80%"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="close()"
    >
      <div class="edit-table">
        <el-table
          ref="editTable"
          :data="editTableData"
          style="width: 100%"
          border
          tooltip-effect="light"
        >
          <el-table-column type="index" label="序号" align="center" width="60">
          </el-table-column>
          <el-table-column prop="PartNameKey" label="关重件" min-width="12%">
            <template slot-scope="scope">
              <div>{{ scope.row['PartNameKey'] }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="PartSerialNumber" label="出厂编号" min-width="12%">
            <template slot-scope="scope">
              <!-- <div>{{ scope.row['PartSerialNumber'] }}</div> -->
              <el-input v-model="scope.row['PartSerialNumber']" placeholder="出厂编号"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="PartBrandName" label="品牌" min-width="12%">
            <template slot-scope="scope">
              <!-- <div>{{ scope.row['PartBrandName'] }}</div> -->
              <el-input v-model="scope.row['PartBrandName']" placeholder="品牌"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="PartModelName" label="型号" min-width="12%">
            <template slot-scope="scope">
              <!-- <div>{{ scope.row['PartModelName'] }}</div> -->
              <el-input v-model="scope.row['PartModelName']" placeholder="型号"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="PartStandardName" label="规格" min-width="12%">
            <template slot-scope="scope">
              <!-- <div>{{ scope.row['PartStandardName'] }}</div> -->
              <el-input v-model="scope.row['PartStandardName']" placeholder="规格"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="PartFactory" label="厂商" min-width="12%">
            <template slot-scope="scope">
              <!-- <div>{{ scope.row['PartFactory'] }}</div> -->
              <el-input v-model="scope.row['PartFactory']" placeholder="厂商"></el-input>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="close()">取 消</el-button>
        <el-button type="primary" @click="submitForm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import { mapGetters } from 'vuex'
export default {
  name: 'crewHistory',
  mixins: [paginationConfigMixin],
  data() {
    return {
      loading: false,
      tableData: [],
      dialigVisible: false,
      editTableData: [],

    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.paginationCfg.currentPage = 1
        this.queryTableData()
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      const parmas = {
        Id: this.selectCrew.ID,
        // page: this.paginationCfg.currentPage,
        // pageSize: this.paginationCfg.pageSize
      }
      this.loading = true
      this.$api.equipment
        .findGetGensetInfoBOMList(parmas, {})
        .then((res) => {
          console.log(res)
          if (res.IsSuccess) {
            this.tableData = res.Data
            this.loading = false
          }
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 添加
    handleAddKeyParts() {
      const parmas = {
        Id: this.selectCrew.ID,
      }
      this.$api.equipment
        .GetAllGensetInfoBOMFoUpdate(parmas, {})
        .then((res) => {
          if (res.IsSuccess) {
            this.editTableData = res.Data
            this.dialigVisible = true
          } else {
            this.$message({
              type: 'error',
              message: res.Message
            })
          }
        })
        .catch((error) => {
          this.$message.error(error)
        })
    },
    // 表单提交
    submitForm() {
      const parmas = {
        GeneratorsetId: this.selectCrew.ID,
        GensetInfoBOMList: this.editTableData
      }
      this.$api.equipment
        .AddGensetInfoBOMList(parmas)
        .then((res) => {
          if (res.IsSuccess) {
            this.$message({
              type: 'success',
              message: res.Message
            })
            this.dialigVisible = false
            this.queryTableData()
          } else {
            this.$message({
              type: 'error',
              message: res.Message
            })
          }
        })
        .catch((error) => {
          this.$message.error(error)
        })
    },

    close() {
      this.dialigVisible = false
    }

  }
}
</script>

<style lang="less" scoped>
.crew-key-parts {
  .crewinfo-card {
    border: 1px solid rgba(0, 0, 0, 0.06);
    border-radius: 4px;
    box-shadow: 0px 2px 1px rgb(0 0 0 / 5%);
    .crewinfo-card-header {
      padding-left: 4px;
      text-transform: capitalize;
      font-weight: 200;
      letter-spacing: 0.4px;
      line-height: 24px;
      // border-bottom: 1px solid rgba(0, 0, 0, 0.06);
    }
  }
  .operate-btns{
    // text-align: right;
    margin-bottom: 16px;
  }
}
</style>



