<template>
  <div class="data-record">
    <!-- <p class="content-title">数据记录</p> -->
    <div class="data-record-container">
      <div class="data-record-container__left">
        <ul>
          <!-- <li>电站控制器配置</li> -->
          <template v-for="item in configList">
            <li
              :key="item.key"
              :class="{ active: item.key == activeNum }"
              @click="changeActive(item)"
            >
              <span>{{ item.value }}</span>
            </li>
          </template>
        </ul>
      </div>
      <div class="data-record-container__right">
        <!-- 报警曲线记录 -->
        <GensetAlarmCurveRecord v-if="activeNum == 1"/>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import GensetAlarmCurveRecord from './GensetAlarmCurveRecord'
const configList = [
  { key: '0', value: '机组履历' },
  { key: '1', value: '报警曲线记录' },
  { key: '2', value: '启动曲线记录' },
  { key: '3', value: '一键报修记录' },
  { key: '4', value: '操作记录' },
  { key: '5', value: '运行记录' },
  { key: '6', value: '上线记录' },
  { key: '7', value: '发电量统计' },
  { key: '8', value: '报警历史' },
  { key: '9', value: '历史曲线' }
]
export default {
  name: 'CrewDataRecord',
  components: {
    GensetAlarmCurveRecord
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    // 监听选中机组切换
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.GensetId = newVal.ID
        // this.reload()
      },
      deep: true
    }
  },
  data() {
    return {
      GensetId: null,
      configList,
      activeNum: null
    }
  },
  mounted() {
    this.GensetId = this.selectCrew.ID
    this.activeNum = this.$route.meta.active || '0'
  },

  methods: {
    changeActive(val) {
      this.activeNum = val.key
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.data-record {
  display: initial !important;
  .content-container {
    display: flex;
    margin-bottom: 16px;
    .content-container-item {
      flex: 1;
      &:first-child {
        margin-right: 16px;
      }
    }
    ul {
      border: 1px solid #ebeef5;
      display: flex;
      flex-wrap: wrap;
      padding: 8px;
      li {
        width: 33.33%;
        height: 32px;
        line-height: 32px;
      }
    }
  }

  .data-record-container {
    position: relative;
    // padding: 0 16px;
    &__left {
      width: 120px;
      height: 100%;
      border-right: 1px solid #e7e7e7;
      position: absolute;
      ul {
        margin-right: -1px;
        border: 1px solid #e7e7e7;
        > li {
          line-height: 32px;
          cursor: pointer;
          text-align: center;
          &:not(:last-child) {
            border-bottom: 1px solid #e7e7e7;
          }
          &.active {
            background-color: @theme;
          }
        }
      }
    }
    &__right {
      margin-left: 120px;
      padding-left: 16px;
      position: relative;
      height: 100%;
    }
  }
}

.theme-dark .data-record .data-record-container {
  &__left {
    border-right: 1px solid @borderColor;
    ul {
      border: 1px solid @borderColor;
      > li:not(:last-child) {
        border-bottom: 1px solid @borderColor;
      }
    }
    /deep/.el-collapse {
      .el-collapse-item__header {
        border-bottom: 1px solid @borderColor;
      }
    }
  }
}
</style>



