<!--
 * @Autor: linxu
 * @Date: 2022-06-15 15:50:34
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 17:00:26
 * @Description: 起动可靠性分析报告
-->
<template>
  <div class="starting-reliability-report">
    <div class="search-box">
      <div class="search-item">
        <span>筛选:</span>
        <el-date-picker
          v-model="searchTime"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd HH:mm"
          format="yyyy-MM-dd HH:mm"
          :picker-options="pickerTime"
          placement="bottom-start"
          @change="timeChange()"
        >
        </el-date-picker>
      </div>
      <el-button type="primary" @click="handleSearch()">搜索</el-button>
      <el-button type="primary" icon="el-icon-download" @click="downloadFile">下载</el-button>
    </div>
    <div id="descriptions-box" class="descriptions-box">
      <!-- 发电机组试验报告 -->
      <p class="part-title bold">发电机组起动可靠性趋势分析报表</p>
      <el-descriptions class="part-content descriptions_part_1" title="" :column="5" border>
        <template v-for="(item, index) in descriptionsItems1">
          <el-descriptions-item :key="index" :span="item['size']">
            <template slot="label">{{ item['label'] }}</template>
            <div class="description-content-box">
              <!-- <el-input v-model="dataSource[item['key']]"
                        placeholder=""></el-input> -->
              <span>{{ dataSource && dataSource[item['key']] }}</span>
              <span v-if="item['unit']">{{ item['unit'] }}</span>
            </div>
          </el-descriptions-item>
        </template>
      </el-descriptions>
      <div class="curve-container">
        <span
          >海拔高度：{{ dataSource && dataSource['海拔'] ? `${dataSource['海拔']} m` : '-' }}</span
        >
        <BaseEcharts
          v-if="startingFlag"
          class="curve-box"
          :containerId="`StartingReliabilityCurve`"
          :grid="startingGrid"
          :legendData="startingLegendData"
          :xAxisData="startingXAxisData"
          :yAxisData="startingYAxisData"
          :seriesData="startingSeriesData"
          :colors="startingColors"
        />
        <!-- :toolboxCfg="toolboxCfg"
        :tooltip="tooltip" -->
      </div>
    </div>
  </div>
</template>

<script>
import { htmlToPdf } from '@/utils/util'
import BaseEcharts from '@/components/BaseEcharts'
import { mapGetters } from 'vuex'
import dayjs from 'dayjs'
const descriptionsItems1 = [
  {
    label: '发电机组型号',
    key: '发电机组型号',
    unit: '',
    size: ''
  },
  {
    label: '原动力型号',
    key: '原动力型号',
    unit: '',
    size: ''
  },
  {
    label: '发动机型号',
    key: '发动机型号',
    unit: '',
    size: ''
  },
  {
    label: '电机型号',
    key: '电机型号',
    unit: '',
    size: ''
  },
  {
    label: '控制器型号',
    key: '控制器型号',
    unit: '',
    size: ''
  },
  {
    label: '发电机组序列号',
    key: '发电机组序列号',
    unit: '',
    size: ''
  },
  {
    label: '执行器型号',
    key: '执行器型号',
    unit: '',
    size: ''
  },
  {
    label: '发动机序列号',
    key: '发动机序列号',
    unit: '',
    size: ''
  },
  {
    label: '电机序列号',
    key: '电机序列号',
    unit: '',
    size: ''
  },
  {
    label: 'AVR型号',
    key: 'AVR型号',
    unit: '',
    size: ''
  },
  {
    label: '视在功率',
    key: '发电机组视在功率',
    unit: 'kVA',
    size: ''
  },
  {
    label: '功率因数',
    key: '发电机组功率因数',
    unit: '',
    size: ''
  },
  {
    label: '额定频率',
    key: '发电机组额定频率',
    unit: 'Hz',
    size: ''
  },
  {
    label: '额定转速',
    key: '发电机组额定转速',
    unit: 'r/min',
    size: '2'
  },
  {
    label: '额定功率',
    key: '发电机组额定功率',
    unit: 'kW',
    size: ''
  },
  {
    label: '额定电压',
    key: '发电机组额定电压',
    unit: 'V',
    size: ''
  },
  {
    label: '额定电流',
    key: '发电机组额定电流',
    unit: 'A',
    size: ''
  },
  {
    label: '励磁方式',
    key: '',
    unit: '',
    size: '2'
  },
  {
    label: '相对湿度',
    key: '相对湿度',
    unit: '',
    size: ''
  },
  {
    label: '环境温度',
    key: '环境温度',
    unit: '',
    size: '1.5'
  },
  {
    label: '大气压力',
    key: '大气压力',
    unit: '',
    size: ''
  },
  {
    label: '海拔高度',
    key: '海拔',
    unit: 'm',
    size: ''
  }
]
const option = {
  id: 'StartingReliabilityCurve',
  label: '起动曲线',
  echartsItems: [
    {
      label: '电池电压',
      key: 'Leijctcout',
      accuracy: 1,
      type: 'line',
      yIndex: 0,
      data: []
    },
    {
      label: '水温',
      key: 'LeiJdn',
      accuracy: 1,
      type: 'line',
      yIndex: 1,
      data: []
    },
    {
      label: '油压',
      key: 'Fzqsgl',
      accuracy: 1,
      type: 'line',
      yIndex: 2,
      data: []
    },
    {
      label: '燃油位',
      key: 'Fzjsgl',
      accuracy: 1,
      type: 'line',
      yIndex: 3,
      data: []
    },
    {
      label: '蓄电池1起动电压跌落',
      key: 'Reserve2',
      accuracy: 1,
      type: 'line',
      yIndex: 4,
      data: []
    },
    {
      label: '蓄电池2起动电压跌落',
      key: 'Reserve3',
      accuracy: 1,
      type: 'line',
      yIndex: 4,
      data: []
    },
    {
      label: '起动成功时间',
      key: 'Reserve4',
      accuracy: 1,
      type: 'line',
      yIndex: 4,
      data: []
    }
  ],
  yAxisItems: [
    {
      label: '电池电压', // 电池电压
      unit: ''
    },
    {
      label: '水温', // 水温
      unit: ''
    },
    {
      label: '油压', // 油压
      unit: ''
    },
    {
      label: '燃油位', // 燃油位
      unit: ''
    },
    {
      label: '电压跌落', // 电压跌落
      unit: ''
    }
  ]
}

export default {
  name: 'StartingReliabilityReport',
  components: {
    BaseEcharts
  },
  data() {
    return {
      selectTab: this.$route.query.selectTab,
      searchTime: null,
      pickerTime: {
        disabledDate: (time) => {
          return time.getTime() > Date.now()
        }
      },

      descriptionsItems1, // 发电机组试验报告 描述项
      dataSource: null, // 发电机组试验报告数据、
      data: null,
      // 起动曲线
      option,
      startingFlag: false,
      startingGrid: {
        x: 50,
        y: 24,
        x2: 150,
        y2: 24,
        top: '15%'
      },
      startingColors: [
        '#EF7B10',
        '#BD2B0B',
        '#CAD400',
        '#56D43E',
        'blue',
        '#45EFB1',
        '#EF7B10',
        '#F2055C'
      ],
      startingLegendData: [],
      startingXAxisData: [],
      startingYAxisData: [],
      startingSeriesData: []
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.getCurveData()
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    this.initSearchTime()
    this.getCurveData()
  },
  methods: {
    // 筛选时间 初始化
    initSearchTime() {
      const endTime = new Date()
      const startTime = new Date(endTime.getTime() - 3600 * 1000 * 24 * 30)
      const end = dayjs(endTime).format('YYYY-MM-DD HH:mm')
      const start = dayjs(startTime).format('YYYY-MM-DD HH:mm')
      this.searchTime = [start, end]
      console.log(this.searchTime)
    },
    timeChange() {
      console.log(this.searchTime)
    },
    handleSearch() {
      if (!this.searchTime || this.searchTime.length == 0) {
        this.initSearchTime()
      }
      this.getCurveData()
    },
    // 获取曲线详情数据
    async getCurveData() {
      const params = {
        Type: '启动记录',
        TriggerId: 1200,
        TriggerName: '启动时间',
        StartTime: this.searchTime && this.searchTime[0],
        EndTime: this.searchTime && this.searchTime[1]
      }
      const query = {
        generatorId: this.selectCrew.ID
      }
      // 获取各项目数据 和 数据精度
      let Precisions = null
      let data = await this.$api.equipment
        .GetGeneratorSetRecordDailyList(params, query)
        .then((res) => {
          if (res.Code === 1) {
            return res.Data
          }
        })
      console.log(data, Precisions)

      // 记录表数据赋值
      this.$set(this, 'dataSource', data.dic)
      this.$set(this, 'data', data.Data.Datas)

      this.startingInit()
    },
    // 组合曲线初始化
    startingInit() {
      const curveData = {
        Leijctcout: [], // 电池电压
        LeiJdn: [], // 水温
        Fzqsgl: [], // 油压
        Fzjsgl: [], // 燃油位
        Reserve2: [], // 蓄电池1起动电压跌落
        Reserve3: [], // 蓄电池2起动电压跌落
        Reserve4: [], // 起动成功时间
        RecordDate: [] // 时间戳
      }
      this.data.map((dataItem) => {
        curveData.Leijctcout.push(dataItem.Leijctcout)
        curveData.LeiJdn.push(dataItem.LeiJdn)
        curveData.Fzqsgl.push(dataItem.Fzqsgl)
        curveData.Fzjsgl.push(dataItem.Fzjsgl)
        curveData.Reserve2.push(dataItem.Reserve2)
        curveData.Reserve3.push(dataItem.Reserve3)
        curveData.Reserve4.push(dataItem.Reserve4)
        curveData.RecordDate.push(this.$moment(dataItem.Record_Date).format('YYYYMMDDHHmmss'))
      })
      console.log(curveData)
      this.startingXAxisData = [
        {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {
            // interval: 4
          },
          splitLine: {
            show: true,
            width: 5,
            lineStyle: {
              type: 'dotted',
              color: '#6E7079'
            }
          },
          boundaryGap: false,
          data: curveData.RecordDate
        }
      ]
      const startingLegendData = []
      const startingYAxisData = []
      const startingSeriesData = []

      this.option.echartsItems.forEach((item, index) => {
        // 设置legendData
        startingLegendData.push(item.label)

        // 设置seriesData
        const seriesDataChildItem = {
          name: item.label,
          type: item.type,
          yAxisIndex: item.yIndex,
          data: curveData[item.key] == null ? [] : curveData[item.key],
          showAllSymbol: false
        }
        startingSeriesData.push(seriesDataChildItem)
      })
      this.option.yAxisItems.forEach((item, index) => {
        // yAxisData
        const yAxisDataChildItem = {
          type: 'value',
          name: item.label,
          min: item.min || 0,
          // max: 250,
          position: index === 0 ? 'left' : 'right',
          offset: index >= 2 ? (index - 1) * 42 : 0,
          axisLine: {
            show: true,
            lineStyle: {
              color: this.startingColors[index]
            }
          },
          axisLabel: {
            formatter: `{value} ${item.unit || ''}`
          },
          splitLine: {
            lineStyle: {
              type: 'dotted',
              color: '#6E7079'
            }
          }
        }
        startingYAxisData.push(yAxisDataChildItem)
      })
      // console.log(startingLegendData, startingYAxisData, startingSeriesData)
      this.$set(this, 'startingLegendData', startingLegendData)
      this.$set(this, 'startingYAxisData', startingYAxisData)
      this.$set(this, 'startingSeriesData', startingSeriesData)
      this.$nextTick(() => {
        this.startingFlag = true
      })
    },
    // 下载
    downloadFile() {
      const props = {
        title: '发电机组起动可靠性趋势分析报表',
        contentId: '#descriptions-box'
      }
      htmlToPdf(props)
    }
  }
}
</script>

<style lang="less" scoped>
@borderColor: #ebeef5;
@borderBottom: {
  border-bottom: 1px solid @borderColor;
};
.starting-reliability-report {
  .search-box {
    margin-bottom: 16px;
    display: flex;
    justify-content: flex-start;
    background-color: #fff;
    .search-item {
      width: 50%;
      > span {
        margin-right: 8px;
      }
    }
  }
  @fontColor: {
    color: #000;
  };

  /deep/.descriptions-box {
    p.part-title {
      @borderBottom();
      line-height: 32px;
      text-align: center;
      padding: 4px;
      box-sizing: border-box;
      &.bold {
        line-height: 40px;
        font-weight: bold;
        font-size: 22px;
        border-bottom: 0;
      }
      &.text-left {
        text-align: left;
      }
    }
    .part-content {
      margin: 0 -1px;
    }
  }

  .descriptions-box {
    border: 1px solid @borderColor;
    padding-bottom: 16px;
    // border-bottom: 0;
    /deep/.el-descriptions.descriptions_part_1 {
      .el-descriptions-item__cell {
        width: 200px;
        padding: 10px 8px;
        // @fontColor();
        &.el-descriptions-item__label.is-bordered-label {
          text-align: center;
        }
      }
    }
    .description-content-box {
      display: flex;
      align-items: center;
      /deep/.el-input {
        .el-input__inner {
          border: 0;
          padding: 0;
        }
      }
      > span {
        &:first-child {
          flex: 1;
        }
        margin-left: 2px;
      }
    }
  }

  .curve-container {
    line-height: 40px;
    > span {
      display: inline-block;
      width: 30%;
      padding-left: 8px;
      font-weight: bold;
      font-size: 16px;
    }
    /deep/.curve-box {
      height: 450px;
      padding: 5px 4px;
    }
  }
}

.theme-dark #descriptions-box {
  background-color: #102447;
}
</style>



