<!--
 * @Autor: linxu
 * @Date: 2022-06-09 09:30:04
 * @LastEditors: linxu
 * @LastEditTime: 2022-06-20 11:06:11
 * @Description: 添加推送消息弹窗
-->
<template>
  <el-dialog
    title="添加推送消息"
    class="add-message-dialog"
    width="50%"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <el-form :model="form" :rules="rules" ref="form" :label-width="formLabelWidth">
      <el-form-item label="机组名称" prop="DisplayName">
        <p>{{ form.DisplayName }}</p>
      </el-form-item>
      <el-form-item label="云猫标识" prop="CollectorToken">
        <p>{{ form.CollectorToken }}</p>
      </el-form-item>
      <el-form-item label="类型" prop="type">
        <el-select v-model="form.type" placeholder="请选择租赁类型" @change="handleChangeSelType">
          <el-option
            v-for="(item, index) in GensetCloudMessageTypeEnum"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="选择模板" prop="mould" v-if="form.type !== 0">
        <el-select v-model="form.mould" placeholder="请选择模板" @change="handleChangeSelMould">
          <el-option
            v-for="(item, index) in GensetCloudMessageMoulds"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :disabled="item.disabled"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="轮播内容" prop="content" v-if="form.type !== 0">
        <el-input
          type="textarea"
          class="content-textarea"
          v-model="form.content"
          placeholder="请输入内容"
          maxlength="50"
          show-word-limit
          :disabled="form.type === 0"
        ></el-input>
        <span class="form-item-tip">注意替换模板中的“___”</span>
      </el-form-item>

      <el-form-item label="轮播时长" v-if="form.type !== 0">
        <div class="input-group">
          <el-input-number
            v-model="form.hour"
            controls-position="right"
            @change="handleChangeHour"
            :min="0"
            :max="999"
            :disabled="form.type === 0"
          ></el-input-number>
          <span>小时</span>
          <el-input-number
            v-model="form.minute"
            controls-position="right"
            @change="handleChangeMinute"
            :min="0"
            :max="59"
            :disabled="form.type === 0"
          ></el-input-number>
          <span>分钟</span>
          <el-input-number
            v-model="form.second"
            controls-position="right"
            @change="handleChangeSecond"
            :min="0"
            :max="59"
            :disabled="form.type === 0"
          ></el-input-number>
          <span>秒</span>
        </div>
        <span class="form-item-tip">若轮播时长设置为0或不填写，则代表永久轮播。</span>
      </el-form-item>
    </el-form>

    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('form')">取 消</el-button>
      <el-button type="primary" @click="submitForm('form')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { mapGetters } from 'vuex'
import _data from './data.js'
const { GensetCloudMessageTypeEnum, GensetCloudMessageMoulds } = _data
export default {
  name: 'AddCrewDialog',
  props: {},
  data() {
    return {
      loading: false,
      dialigVisible: true,
      GensetCloudMessageTypeEnum,
      GensetCloudMessageMoulds,
      // 表单
      formLabelWidth: '120px',
      form: {
        DisplayName: '',
        CollectorToken: '',
        type: null,
        mould: null,
        content: null,
        hour: '',
        minute: '',
        second: ''
      },
      rules: {
        type: [{ required: true, message: '请选择类型', trigger: 'change' }],
        mould: [{ required: true, message: '请选择模板', trigger: 'change' }],
        content: [{ required: true, message: '请输入轮播内容', trigger: 'blur' }]
      }
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  mounted() {
    console.log(this.selectCrew)
    this.form.DisplayName = this.selectCrew.DisplayName
    this.form.CollectorToken = this.selectCrew.CollectorToken
  },
  methods: {
    // 切换选择类型
    handleChangeSelType(val) {
      console.log(val)
      this.form.mould = null
    },
    // 切换选中模板
    handleChangeSelMould(val) {
      console.log(val, this.form.mould)
      let text = this.GensetCloudMessageMoulds.find((item) => item.value == val).label
      text = text.replace('**', '____').replace('xxx', '____')
      this.$set(this.form, 'content', text)
    },
    // 修改轮播时长
    handleChangeHour() {},
    handleChangeMinute() {},
    handleChangeSecond() {},
    // 表单提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.form)
          // 轮播时长 转换为 s
          const saveTimes =
            (this.form.hour == undefined ? 0 : parseInt(this.form.hour) * 3600) +
            (this.form.minute == undefined ? 0 : parseInt(this.form.minute) * 60) +
            (this.form.second == undefined ? 0 : parseInt(this.form.second))
          const params = {
            GeneratorSetId: this.selectCrew.ID,
            Type: this.form.type,
            Values: this.form.content,
            SaveTimes: saveTimes
          }
          this.$api.operation
            .AddMessagePush(params)
            .then((res) => {
              if (res.Code === 1) {
                console.log(res)

                this.$message({
                  type: 'success',
                  message: res.Message
                })
                this.$emit('closeDialog', { refresh: true })
              }
            })
            .catch(() => {})
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.close()
    },
    close() {
      this.$emit('closeDialog')
    }
  }
}
</script>

<style lang="less" scoped>
.add-message-dialog {
  /deep/ .el-form .el-form-item {
    &.is-required {
      margin-bottom: 22px;
    }
    .el-form-item__content {
      flex-direction: column;
      .form-item-tip {
        text-align: right;
        font-size: 12px;
        // color: #909399;
        opacity: 0.8;
        position: absolute;
        top: 100%;
        right: 0;
        line-height: 20px;
      }
    }
  }
  .content-textarea {
    .el-input__count {
      line-height: 1.5;
    }
  }
  .input-group {
    width: 100%;
    display: flex;
    > span {
      white-space: nowrap;
      padding: 0 4px;
    }
  }
}
</style>



