<!--
 * @Autor: linxu
 * @Date: 2022-06-09 09:30:04
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-01 10:53:10
 * @Description: 云消息推送列表
-->
<template>
  <div>
    <!-- <BMainTopComponent /> -->
    <el-container>
      <el-main>
        <div class="lease-genset-manage" v-if="IsGenMsgPush">
          <!-- 筛选 -->
          <div class="search-box">
            <div class="search-item">
              <span>筛选:</span>
              <el-select
                v-model="searchForm.Type"
                :popper-append-to-body="false"
                placeholder="请选择类型"
              >
                <el-option label="全部" value="-1"></el-option>
                <el-option
                  v-for="item in GensetCloudMessageTypeEnum"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </div>
            <div class="search-btn-box">
              <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
              <el-button type="primary" class="query-btn" @click="resetCurrentParams()"
                >查 询</el-button
              >
            </div>
          </div>

          <!-- 操作按钮   -->
          <div class="operation-box">
            <el-button type="primary" @click="handleAddLeaseGenset()"> 添加推送消息 </el-button>
          </div>

          <!-- 表格 -->
          <TableList
            :styleList="{ height: '100%' }"
            :options="options"
            :columns="columns"
            :dataSource="dataSource"
            :dataTotal="dataTotal"
            :pagination="paginationCfg"
            @handleChangePage="handleChangePage"
            @handleChangeSize="handleChangeSize"
            border
            v-loading="loading"
          >
            <el-table-column
              slot="operation"
              label="操作"
              align="center"
              v-bind="options && options.props"
            >
              <template slot-scope="scope">
                <div class="operate-group">
                  <el-button type="text" @click="handleDelete(scope.row)"> 移除 </el-button>
                </div>
              </template>
            </el-table-column>
          </TableList>
        </div>
        <div v-else v-loading="loading">
          <div class="no-genmsg-push">当前控制器暂未支持云消息推送</div>
        </div>
      </el-main>
      <!-- <toggle-crew></toggle-crew> -->
    </el-container>

    <!-- 添加云消息推送弹窗 -->
    <AddLeaseGensetDialog
      v-if="crewManageVisible"
      @closeDialog="(val) => closeDialog('crewManageVisible', val)"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import ToggleCrew from '@/components/toggleCrew/list'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import AddLeaseGensetDialog from './add'
import _data from './data.js'
const { GensetCloudMessageTypeEnum } = _data

export default {
  name: 'LeaseGenset',
  mixins: [paginationConfigMixin],
  components: {
    ToggleCrew,
    AddLeaseGensetDialog
  },
  data() {
    return {
      loading: false,
      IsGenMsgPush: false,
      // 筛选条件
      searchForm: {
        Type: '-1'
      },
      GensetCloudMessageTypeEnum,
      // 表格
      columns: _data.leaseGensetColumns(this),
      dataSource: [],
      options: {
        // height: '100%',
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 86 // 操作列宽度
        }
      },
      data: [],

      crewManageVisible: false, // 添加弹窗
      recordInfo: null
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    // 监听选中机组切换
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.paginationCfg.currentPage = 1
        this.FindGeneratorSet()
      },
      deep: true
    },
    // 跳转路由对应页签与当前页签相同，组件复用，未重新查询数据
    $route: {
      handler: function (to, from) {
        if (from) {
          this.FindGeneratorSet()
        }
      },
      deep: true,
      immediate: true
    }
  },
  mounted() {
    this.FindGeneratorSet()
  },
  methods: {
    initData() {
      this.FindGeneratorSet()
    },
    // 获取设备信息--判断设备云消息是否使用
    FindGeneratorSet() {
      this.loading = true
      this.$api.operation
        .FindGeneratorSet({ id: this.selectCrew.ID })
        .then((res) => {
          if (res.Code === 1) {
            this.IsGenMsgPush = res.Data.IsGenMsgPush
            // IsGenMsgPush为true 云消息推送功能 可用
            if (!res.Data.IsGenMsgPush) {
              this.loading = false
              return
            }
            this.queryTableData()
          }
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 获取设备云消息历史列表
    queryTableData() {
      this.loading = true
      const query = {
        Page: this.paginationCfg.currentPage,
        PageSize: this.paginationCfg.pageSize
      }
      const params = {
        GeneratorSetId: this.selectCrew.ID,
        ...this.searchForm
      }
      this.$api.operation
        .GetMessageList(params, query)
        .then((res) => {
          if (res.Code === 1) {
            console.log(res)
            this.dataTotal = res.Data.TotalItemCount
            const data = res.Data.Data
            this.$set(this, 'dataSource', data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 重置筛选条件
    reset() {
      this.$set(this, 'searchForm', {
        Type: '-1'
      })
    },

    // 添加云消息推送
    handleAddLeaseGenset() {
      this.crewManageVisible = true
    },

    // 删除云消息历史
    handleDelete(row) {
      this.$api.operation
        .DeleteMessagePush({ id: row.GeneratorSetId })
        .then((res) => {
          if (res.Code === 1) {
            this.queryTableData()
            this.$message({
              type: 'success',
              message: res.Message
            })
          }
        })
        .catch(() => {})
    },

    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.queryTableData()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.lease-genset-manage {
  display: initial !important;
  .search-box {
    .search-item {
      // width: 20%;
    }
  }
}

.no-genmsg-push {
  line-height: 400px;
  text-align: center;
  font-size: 1.5rem;
}
</style>



