<!--
 * @Autor: linxu
 * @Date: 2022-04-26 16:14:30
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-21 17:26:01
 * @Description: 机组详情-控制器配置
-->
<template>
  <!-- <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    /> -->
  <div class="controller-configuration">
    <!-- <p class="content-title">机组配置</p> -->
    <!-- <div class="config-controller">
        机组可用性：
        <el-switch
          class="config-switch"
          v-model="GensetConfigureSetting.IsGensetConfigure"
          @input="(val) => handleInput(val)"
          @click.native.prevent="handleClickSwitch()">
        </el-switch>
        <span class="tip">{{GensetConfigureSetting.IsGensetConfigure ? '可用时下方配置生效':'不可用时下方配置失效'}}</span>
      </div> -->
    <div class="config-container">
      <div class="config-container__left">
        <ul>
          <li>控制器配置</li>
          <template v-for="(item, index) in configList">
            <li
              :key="index"
              v-if="!item.IsOpr"
              :class="{ active: item.Type == activeNum }"
              @click="changeActive(item)"
            >
              <span>{{ index + 1 }}. {{ item.TypeName }}</span>
              <!-- （{{ item.status ? '已完成' : '待完成' }}） -->
            </li>
            <el-collapse :key="index" v-else>
              <el-collapse-item :name="index">
                <template slot="title">
                  <span> {{ index + 1 }}. {{ item.TypeName }}</span>
                </template>
                <ol>
                  <template v-for="(subItem, subIndex) in item.TypeList">
                    <li
                      :key="subIndex"
                      :class="{ active: subItem.Type == activeNum }"
                      @click="changeActive(subItem)"
                    >
                      {{ subItem.TypeName }}
                    </li>
                  </template>
                </ol>
              </el-collapse-item>
            </el-collapse>
          </template>
        </ul>
      </div>
      <div class="config-container__right">
        <!-- 限功标定 -->
        <power-limit-form
          ref="powerLimitForm"
          v-if="activeNum == 11"
          :formData="formData"
          @handleSave="handleSave"
          @handleRefreshClick="handleRefreshClick"
          @handleReadClick="handleReadClick"
        ></power-limit-form>

        <!-- 维保策略配置 -->
        <strategy-config-form
          ref="strategyConfigForm"
          v-else-if="activeNum == 14"
          :formData="formData"
          :gensetId="GensetId"
          @handleSave="handleSave"
          @handleRefreshClick="handleRefreshClick"
          @handleReadClick="handleReadClick"
        ></strategy-config-form>

        <!-- 数据重置 -->
        <div v-else-if="activeNum == 15" class="data-config-container">
          <div>
            <config-form ref="ConfigForm" :isEdit="isEdit" :formData="formData"></config-form>
            <!-- <div class="genset-data">
                <p>期初底座油箱油量：300L</p>
                <p>期末底座油箱油量：30L</p>
                <p>加油次数：3</p>
                <p>机组燃油用量：470L</p>
                <p>机组平均油耗：33.33g/Kwh</p>
                <p>机组平均运行时间：480 h 35 min</p>
                <p>机组发电量：2345.55Kwh</p>
                <p>机组无故障时间：2000 h 35 min</p>
                <p>机组超功率运行时间：72 h 35 min</p>
              </div> -->
            <div class="btn-container">
              <el-button type="primary" v-if="!isEdit" @click="handleRefreshClick">刷新</el-button>
              <el-button
                type="primary"
                v-if="!isEdit"
                :disabled="formData.IsReadIng"
                @click="handleReadClick"
                >读取</el-button
              >
              <el-button
                type="primary"
                v-if="!!formData && !formData.IsReadIng && !formData.IsWriteIng"
                @click="handleReset"
                >立即重置</el-button
              >
            </div>
          </div>
        </div>

        <!-- 表单数据 -->
        <div v-else>
          <config-form ref="ConfigForm" :isEdit="isEdit" :formData="formData"></config-form>
          <div
            class="btn-container"
            v-if="!!formData && !!formData.DetailList && formData.DetailList.length > 0"
          >
            <el-button type="primary" v-if="!isEdit" @click="handleRefreshClick">刷新</el-button>
            <el-button
              type="primary"
              v-if="!isEdit"
              :disabled="formData.IsReadIng"
              @click="handleReadClick"
              >读取</el-button
            >

            <template v-if="!formData.IsReadIng && !formData.IsWriteIng">
              <el-button type="primary" v-if="!isEdit" @click="handleEditClick">编辑 </el-button>
              <span v-else>
                <el-button @click="handleCancelClick">取消</el-button>
                <el-button type="primary" @click="handleSaveClick">保存</el-button>
              </span>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import _data from './components/data.js'
import ConfigForm from './components/ConfigForm.vue'
import PowerLimitForm from './components/PowerLimitForm.vue'
import StrategyConfigForm from './components/StrategyConfigForm.vue'
const { ProjectTypeEnum, ProjectStatusEnum } = _data
const projectInfoColumns = [
  {
    label: '项目名称',
    prop: 'ProjectName'
  },
  // {
  //   label: '项目类型',
  //   prop: 'ProjectType'
  // },
  {
    label: '项目编号',
    prop: 'ProjectNumber'
  },
  {
    label: '功率需求',
    prop: 'TotalPower',
    unit: 'Kw'
  },
  {
    label: '机组数量',
    prop: 'Quantity',
    unit: '台'
  },
  {
    label: '项目开始时间',
    prop: 'StartDate'
  },
  {
    label: '项目状态',
    prop: 'Status'
  },
  {
    label: '项目结束时间',
    prop: 'EndDate'
  },
  {
    label: '到期剩余时间',
    prop: 'EndDateLast'
  },
  {
    label: '项目地址',
    prop: 'ProjectArea',
    width: '100%'
  }
]
const gensetInfoColumns = [
  {
    label: '机组名称',
    prop: 'DisplayName'
  },
  {
    label: '标识',
    prop: 'CollectorToken'
  },
  {
    label: '发动机型号',
    prop: 'EngineModel'
  },
  {
    label: '功率',
    prop: 'RatedPower',
    unit: 'Kw'
  },
  {
    label: '运行时间',
    prop: 'RunHourTotal',
    unit: 'h'
  },
  {
    label: '是否并机',
    prop: 'IsParallel'
  },
  {
    label: '电子围栏开启情况',
    prop: 'IsFenceOpen'
  },
  {
    label: '维保策略录入情况',
    prop: 'IsStrategy'
  },
  {
    label: '机组位置',
    prop: 'InstallationSite',
    width: '100%'
  }
]
const configList = [
  {
    IsOpr: false,
    Type: 11,
    TypeName: '限功标定',
    TypeList: [],
    subType: 1101
  },
  {
    IsOpr: false,
    Type: 12,
    TypeName: '权重标定',
    TypeList: [],
    subType: 1102
  },
  {
    IsOpr: false,
    Type: 13,
    TypeName: '其他标定',
    TypeList: [],
    subType: 1103
  },
  {
    IsOpr: false,
    Type: 14,
    TypeName: '维保配置',
    TypeList: [],
    subType: 1104
  },
  {
    IsOpr: false,
    Type: 15,
    TypeName: '数据重置',
    TypeList: [],
    subType: 1105
  }
]
export default {
  name: 'ControllerConfiguration',
  components: {
    ConfigForm,
    PowerLimitForm,
    StrategyConfigForm
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    // 监听选中机组切换
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.GensetId = newVal.ID
        this.GetGensetConfigureSetting()
        // this.reload()
      },
      deep: true
    },
    $route: {
      handler: function (newVal, oldVal) {
        this.GensetId = this.selectCrew.ID
        this.GetGensetConfigureSetting()
      },
      // 深度观察监听
      deep: true
    }
  },
  data() {
    return {
      GensetId: null,
      SettingType: 102,

      ProjectTypeEnum,
      ProjectStatusEnum,
      // 表格
      options: {
        index: false, //序号列
        labelIndex: '序号'
      },
      projectInfoColumns, //: _data.projectInfoColumns(this), // 所选项目 表头
      projectInfoData: {},
      loading_1: false,

      gensetInfoColumns, //: _data.gensetInfoColumns(this), // 所选机组 表头
      gensetInfoData: {},
      loading_2: false,
      GensetConfigureSetting: {
        IsGensetConfigure: false, // 判断是否开启控制器协议配置
        ConfigureId: null,
        configList: []
      },
      configList,
      activeConfig: null,
      activeNum: 11,
      isEdit: true,
      formData: null
    }
  },
  mounted() {
    // this.activeNum = this.$route.query.activeTab || 11
    this.GensetId = this.selectCrew.ID
    this.activeConfig = configList.find((item) => item.Type == this.activeNum)
    this.GetGensetConfigureSetting()
  },

  methods: {
    initData() {
      this.GetGensetConfigureSetting()
      if (this.activeNum === 14) {
        this.$refs.strategyConfigForm.getGensetSettingDetail()
        this.$refs.strategyConfigForm.isEditBase = false
        this.$refs.strategyConfigForm.isEdit = false
      } else if (this.activeNum == 11) {
        this.$refs.powerLimitForm.isEdit = false
      } else {
        this.isEdit = false
      }
    },
    // 返回项目详情
    handleBackClick() {
      this.$router.push({ path: '/project/project-detail', query: { active: 2 } })
    },

    // 查询参数设置
    GetGensetConfigureSetting() {
      const params = {
        GensetId: this.GensetId,
        SettingType: this.SettingType
      }
      this.$api.project.GetGensetConfigureSetting(params).then((res) => {
        if (res.Code === 1) {
          // console.log(res.Data)
          const list = res.Data.GensetConfigureProcTypeList
          this.$set(this.GensetConfigureSetting, 'IsGensetConfigure', res.Data.IsGensetConfigure)
          this.$set(this.GensetConfigureSetting, 'ConfigureId', res.Data.ConfigureId)
          this.$set(this.GensetConfigureSetting, 'configList', list)
          if (res.Data.IsGensetConfigure) {
            // this.handleReadClick(this.activeConfig.subType)
            this.GetGensetConfigureDetails(this.activeNum)
          } else {
            this.$set(this, 'formData', {})
          }
        }
      })
    },
    // 查询参数设置详情
    GetGensetConfigureDetails(val) {
      const params = {
        GensetId: this.GensetId,
        SettingType: this.SettingType,
        Type: val,
        ConfigureId: this.GensetConfigureSetting.ConfigureId
      }
      this.$api.project.GetGensetConfigureDetails(params).then((res) => {
        if (res.Code === 1) {
          // console.log('参数设置详情', res.Data)
          let data = res.Data && res.Data.length > 0 ? res.Data[0] : {}
          if (!!data && !!data.DetailList && data.DetailList.length > 0) {
            data.DetailList = data.DetailList.map((item) => {
              return {
                ...item,
                Value: !item.Value && item.Value !== 0 ? undefined : item.Value
              }
            })
          }
          this.$set(this, 'formData', data)
        }
      })
    },

    // 机组可用性控制开关
    handleInput(key, event) {
      this.GensetConfigureSetting.IsGensetConfigure = !event
    },
    handleClickSwitch(key, val) {
      const rowData = AllControlSwitchData.find((item) => item.key == key)
      const activeName = this.switchStatus[`${key}_flag`] ? '关闭' : '开启'
      const title = `${activeName}${rowData.name}`
      const content = `确定${activeName}${rowData.name}吗？`
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: title,
        message: h('div', [h('p', [h('span', content)])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const code = this.switchStatus[`${key}_flag`] ? rowData.closeCode : rowData.openCode
          this.changeSwitchStatus(key, code)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },
    // 切换标定
    changeActive(val) {
      this.isEdit = false
      this.activeConfig = val
      this.activeNum = val.Type

      // this.handleReadClick(this.activeConfig.subType)
      this.GetGensetConfigureDetails(this.activeNum)
    },
    // 点击读取
    handleReadClick() {
      const params = {
        GensetId: this.GensetId,
        SettingType: this.SettingType,
        Type: this.formData.Type
      }
      this.$api.project.GensetConfigureRead(params).then((res) => {
        if (res.Code === 1) {
          // 读取指令发送成功 刷新数据状态
          this.GetGensetConfigureDetails(this.activeNum)
        }
      })
    },

    // 点击保存
    handleSaveClick() {
      this.$refs.ConfigForm.getFormData().then((form) => {
        // console.log(form)
        const params = {
          GensetId: this.GensetId,
          SettingType: this.SettingType,
          Type: this.formData.Type,
          WriteList: form
        }
        this.$api.project.GensetConfigureSave(params).then((res) => {
          if (res.Code === 1) {
            // 写入指令发送成功 刷新数据状态
            this.GetGensetConfigureDetails(this.activeNum)
          }
        })
      })
    },
    // (限功、维保)点击保存
    handleSave(form) {
      const params = {
        GensetId: this.GensetId,
        SettingType: this.SettingType,
        Type: this.formData.Type,
        WriteList: form
      }
      this.$api.project.GensetConfigureSave(params).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: res.Message
          })
          // 写入指令发送成功 刷新数据状态
          this.GetGensetConfigureDetails(this.activeNum)
        }
      })
    },

    // 点击刷新
    handleRefreshClick() {
      this.GetGensetConfigureDetails(this.activeNum)
    },

    // 点击编辑
    handleEditClick() {
      this.isEdit = true
    },
    // 点击取消
    handleCancelClick() {
      this.isEdit = false
      this.GetGensetConfigureDetails(this.activeNum)
    },
    // 数据重置
    handleReset() {
      const content = `确定立即重置当前机组数据吗？`
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: title,
        message: h('div', [h('p', [h('span', content)])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { id: this.GensetId, key: 105 }
          this.$api.operation
            .control({}, query)
            .then((res) => {
              this.$message.success(res.Message)
            })
            .catch((error) => {
              this.$message.error(error)
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.controller-configuration {
  // display: initial !important;
  flex: 1;
  overflow: auto;
  // margin-bottom: 16px;
  .config-controller {
    display: flex;
    margin-bottom: 16px;
    .config-switch {
      width: 52px;
      /deep/.el-switch__core {
        width: 52px !important;
      }
    }
    .tip {
      margin: 4px 0 0 16px;
      font-size: 0.8rem;
      color: #ec808d;
    }
  }
  .config-container {
    height: 100%;
    position: relative;
    display: flex;
    // padding: 0 16px;
    &__left {
      width: 180px;
      // height: 100%;
      margin-right: 16px;
      border-right: 1px solid #e7e7e7;
      // position: absolute;
      ul {
        margin-right: -1px;
        border: 1px solid #e7e7e7;
        > li {
          line-height: 32px;
          &:first-child {
            background-color: rgba(242, 242, 242, 1);
            text-align: center;
          }
          &:not(:last-child) {
            border-bottom: 1px solid #e7e7e7;
          }
          &.active {
            background-color: @theme;
          }
          &:not(:first-child) {
            cursor: pointer;
            padding-left: 24px;
          }
        }
        ol > li {
          line-height: 32px;
          text-align: center;
          border-bottom: 1px solid #e7e7e7;
          cursor: pointer;
          &.active {
            background-color: @theme;
          }
        }
      }
      /deep/.el-collapse {
        border: 0;
        .el-collapse-item:last-child {
          margin-bottom: 0;
        }
        .el-collapse-item__header {
          border-bottom: 1px solid #e7e7e7;
          height: 32px;
          padding-left: 24px;
          background: transparent;
        }
        .el-collapse-item__wrap {
          background: transparent;
          border: 0;
          .el-collapse-item__content {
            padding-bottom: 0;
          }
        }
      }
    }
    &__right {
      flex: 1;
      position: relative;
      > .btn-container {
        position: absolute;
        right: 0;
        top: 0;
      }
      .config-container {
        padding: 16px 0;
      }
    }
  }
}
.data-config-container {
  .genset-data {
    display: flex;
    flex-wrap: wrap;
    padding: 16px;
    p {
      width: 33.33%;
      line-height: 24px;
    }
  }
}

.btn-container {
  text-align: center;
}
.theme-dark .controller-configuration .config-container {
  &__left {
    border-right: 1px solid @borderColor;
    ul {
      border: 1px solid @borderColor;
      > li:first-child {
        background-color: @menuHover;
      }
      > li:not(:last-child) {
        border-bottom: 1px solid @borderColor;
      }
      ol > li {
        border-bottom: 1px solid @borderColor;
      }
    }
    /deep/.el-collapse {
      .el-collapse-item__header {
        border-bottom: 1px solid @borderColor;
      }
    }
  }
}
</style>



