<!--
 * @Autor: linxu
 * @Date: 2022-06-09 09:30:04
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-01 10:56:39
 * @Description: 机组远程解锁机
-->
<template>
  <div>
    <!-- <BMainTopComponent /> -->
    <el-container>
      <el-main>
        <div class="lease-genset-manage" v-if="IsGenLock">
          <div class="genset-info-container">
            <div class="genset-info">
              <p class="content-title">机组信息</p>
              <ul>
                <li v-for="(item, index) in GensetInfoList" :key="index">
                  <p>{{ item.label }}</p>
                  <p>{{ selectCrew[item.key] }}</p>
                </li>
              </ul>
            </div>
            <div class="genset-lock">
              <p class="content-title">机组解锁机</p>
              <div>
                <el-image style="width: 200px; height: 100px" :src="genset_url"></el-image>
              </div>

              <el-row class="btn-container" type="flex">
                <el-col :span="12">
                  <svg-icon
                    icon-class="Icon_lock_true"
                    className="lock-btn"
                    @click="handleEditLockType(1)"
                  ></svg-icon>
                  <p class="lock-text">
                    <span>
                      <em
                        :class="
                          selectCrew.Status != 0 && LockStatus == 1 && LockType == 1 ? 'active' : ''
                        "
                      ></em>
                      锁机</span
                    >
                  </p>
                </el-col>
                <el-col :span="12">
                  <svg-icon
                    icon-class="Icon_lock_false"
                    className="lock-btn"
                    @click="handleEditLockType(0)"
                  ></svg-icon>
                  <p class="lock-text">
                    <span>
                      <em
                        :class="
                          selectCrew.Status != 0 && LockStatus == 1 && LockType == 0 ? 'active' : ''
                        "
                      ></em>
                      解锁</span
                    >
                  </p>
                </el-col>
              </el-row>
            </div>
          </div>
          <p class="content-title">解锁机记录</p>
          <!-- 筛选 -->
          <div class="search-box">
            <div class="search-item date-type">
              <span>筛选:</span>
              <el-date-picker
                class="input-with-select"
                v-model="searchForm['searchTime']"
                type="daterange"
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                range-separator="-"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerTime"
                placement="bottom-start"
              >
              </el-date-picker>
            </div>
            <div class="search-item">
              <span>类型:</span>
              <el-select
                v-model="searchForm['type']"
                :popper-append-to-body="false"
                placeholder="请选择类型"
              >
                <el-option label="全部" value="-1"></el-option>
                <el-option
                  v-for="item in GensetLockTypeEnum"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </div>
            <div class="search-btn-box">
              <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
              <el-button type="primary" class="query-btn" @click="resetCurrentParams()"
                >查 询</el-button
              >
            </div>
          </div>

          <!-- 表格 -->
          <TableList
            :styleList="{ height: '100%' }"
            :options="options"
            :columns="columns"
            :dataSource="dataSource"
            :dataTotal="dataTotal"
            :pagination="paginationCfg"
            @handleChangePage="handleChangePage"
            @handleChangeSize="handleChangeSize"
            border
            v-loading="loading"
          >
          </TableList>
        </div>

        <div v-else v-loading="loading">
          <div class="no-gen-lock">当前控制器暂未支持远程解锁机</div>
        </div>
      </el-main>
      <!-- <toggle-crew></toggle-crew> -->
    </el-container>

    <!-- 编辑弹窗 -->
    <el-dialog
      title="提示"
      class="edit-lock-dialog"
      width="600px"
      v-if="dialigVisible"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="close()"
    >
      <div class="dialog-content">
        <h3>{{ EditType == 0 ? '您确定要远程解锁吗？' : '您确定要远程锁机吗？' }}</h3>
        <p v-if="EditType">执行远程锁机操作后，机组将无法再次启动。请谨慎操作。</p>
        <el-form :model="form" ref="lockForm" label-position="top" :label-width="formLabelWidth">
          <el-form-item :label="EditType == 0 ? '' : '锁机原因'" prop="ProjectNumber">
            <el-input
              type="textarea"
              class="content-textarea"
              v-model="form.Remark"
              placeholder="字符长度不超过200"
              maxlength="200"
            ></el-input>
            <!-- show-word-limit -->
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="close()">取 消</el-button>
        <el-button type="primary" @click="submitForm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import dayjs from 'dayjs'
import { mapActions, mapGetters } from 'vuex'
import ToggleCrew from '@/components/toggleCrew/list'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import _data from './data.js'
const { GensetLockTypeEnum } = _data

const GensetInfoList = [
  { label: '机组名称', key: 'DisplayName' },
  { label: '云猫标识', key: 'CollectorToken' },
  { label: '发动机型号', key: 'EngineModel' },
  { label: '电机型号', key: 'GeneratorModel' },
  { label: '控制器品牌', key: 'ControllerBrand' },
  { label: '控制器型号', key: 'CollectorBrand' }
]

export default {
  name: 'LeaseGenset',
  mixins: [paginationConfigMixin],
  components: {
    ToggleCrew
  },
  data() {
    return {
      loading: false,
      IsGenLock: false, // 是否开启远程解锁机功能
      LockStatus: null, //状态    1为成功状态
      LockType: null, //类型     0为解锁   1为锁机

      GensetInfoList,
      // 筛选条件
      searchForm: {
        searchTime: null,
        type: '-1'
      },
      pickerTime: {
        disabledDate: (time) => {
          return time.getTime() > Date.now() - 8.64e6
        }
      },

      GensetLockTypeEnum,
      // 表格
      columns: _data.leaseGensetColumns(this),
      dataSource: [],
      options: {
        // height: '100%',
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 126 // 操作列宽度
        }
      },
      data: [],

      genset_url: require('@/assets/img/crewmanage/genset-preview.png'),

      dialigVisible: false, // 编辑弹窗
      EditType: null,
      formLabelWidth: '100px',
      form: {
        Remark: ''
      },

      animation: null
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    // 监听选中机组切换
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.FindGeneratorSet()
        this.getLockStatus()
      },
      deep: true
    },
    // 跳转路由对应页签与当前页签相同，组件复用，未重新查询数据
    $route: {
      handler: function (to, from) {
        if (from) {
          this.initData()
        }
      },
      deep: true,
      immediate: true
    }
  },
  mounted() {
    this.initData()
    this.animation = setInterval(() => {
      this.getLockStatus()
    }, 1000 * 10)
  },
  methods: {
    initData() {
      this.initSearchTime()
      this.FindGeneratorSet()
      this.getLockStatus()
    },
    // 筛选时间 初始化
    initSearchTime() {
      const endTime = new Date()
      const startTime = new Date(endTime.getTime() - 3600 * 1000 * 24 * 7)
      const end = dayjs(endTime).format('YYYY-MM-DD')
      const start = dayjs(startTime).format('YYYY-MM-DD')
      this.searchForm.searchTime = [start, end]
    },
    // 获取设备信息--判断设备远程解锁机是否使用
    FindGeneratorSet() {
      this.loading = true
      this.$api.operation
        .FindGeneratorSet({ id: this.selectCrew.ID })
        .then((res) => {
          if (res.Code === 1) {
            this.IsGenLock = res.Data.IsGenLock
            // IsGenLock为true 远程解锁机功能 可用
            if (!res.Data.IsGenLock) {
              this.loading = false
              return
            }
            this.queryTableData()
          }
        })
        .catch(() => {
          this.loading = false
        })
    },
    getLockStatus() {
      this.$api.operation
        .GetLockGenSetStatus({ generatorId: this.selectCrew.ID })
        .then((res) => {
          if (res.Code === 1) {
            console.log(res)
            this.LockStatus = res.Data.status
            this.LockType = res.Data.type
          }
        })
        .catch(() => {})
    },
    queryTableData() {
      this.loading = true
      const query = {
        Page: this.paginationCfg.currentPage,
        PageSize: this.paginationCfg.pageSize
      }
      const params = {
        GeneratorSetId: this.selectCrew.ID,
        Type: this.searchForm.type,
        DateStart: this.searchForm.searchTime && this.searchForm.searchTime[0],
        DateEnd: this.searchForm.searchTime && this.searchForm.searchTime[1]
      }
      this.$api.operation
        .GetLockRecords(params, query)
        .then((res) => {
          if (res.Code === 1) {
            this.dataTotal = res.Data.TotalItemCount
            const data = res.Data.Data
            this.$set(this, 'dataSource', data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 重置筛选条件
    reset() {
      this.$set(this, 'searchForm', {
        searchTime: null,
        type: '-1'
      })
    },

    // 解锁机执行弹窗
    handleEditLockType(val) {
      this.EditType = val
      // if (val !== this.LockType) {
      this.form.Remark = ''
      this.dialigVisible = true
      // }
    },
    // 表单提交
    submitForm() {
      const params = {
        GeneratorSetId: this.selectCrew.ID,
        Type: this.EditType,
        ...this.form
      }
      this.$api.operation
        .RemoteLockGenSet(params)
        .then((res) => {
          if (res.Code === 1) {
            this.$message({
              type: 'success',
              message: res.Message
            })
          } else {
            this.$message({
              type: 'error',
              message: res.Message
            })
          }
          this.closeDialog('dialigVisible', { refresh: true })
        })
        .catch(() => {})
    },
    close() {
      this.closeDialog('dialigVisible')
    },
    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.queryTableData()
      }
    }
  },
  beforeDestroy() {
    clearInterval(this.animation)
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.lease-genset-manage {
  display: initial !important;
  .search-box {
    .search-item {
      // width: 20%;
    }
  }
  .genset-info-container {
    display: flex;
    margin-bottom: 16px;
    > div {
      width: 50%;
      &.genset-info {
        margin-right: 16px;
        ul > li {
          display: flex;
          line-height: 24px;
          > p:first-child {
            width: 30%;
            text-align: right;
            padding-right: 8px;
          }
          > p:last-child {
            flex: 1;
            padding-left: 8px;
          }
        }
      }
      &.genset-lock {
        div {
          text-align: center;
        }
        .btn-container {
          width: 65%;
          margin: 0 auto;
          display: flex;
          justify-content: space-between;
          .lock-btn {
            border: 1px solid;
            border-radius: 50%;
            padding: 6px;
            font-size: 20px;
            cursor: pointer;
          }
          .lock-text {
            margin-top: 4px;
            line-height: 20px;
            span {
              position: relative;
              > em {
                display: inline-block;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                position: absolute;
                left: -16px;
                top: 5px;
                background: #bbbbbb;

                &.active {
                  background: @theme;
                }
              }
            }
          }
        }
      }
    }
  }
}
.edit-lock-dialog {
  .dialog-content {
    > h3 {
      margin-bottom: 8px;
      font-size: 1.2rem;
      text-align: center;
    }
    > p {
      margin: 0 0 4px;
      text-align: center;
    }
    /deep/.el-form--label-top .el-form-item__label {
      padding-bottom: 4px;
    }
  }
}
.no-gen-lock {
  line-height: 400px;
  text-align: center;
  font-size: 1.5rem;
}
</style>



