<!--
 * @Autor: linxu
 * @Date: 2021-12-30 13:51:33
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-25 15:29:49
 * @Description: 机组履历信息
-->
<template>
  <div class="crew-history">
    <div class="crewinfo-card">
      <!-- <div class="crewinfo-card-header">机组履历</div> -->
      <div class="operate-btns">
        <el-button
          type="primary"
          class="addHistoryBtn"
          @click="handleAddHistory()"
        >
          添加
        </el-button>
      </div>    
      <div class="crewinfo-card-content">
        <el-table
          ref="mainTable"
          :data="tableData"
          style="width: 100%"
          border
          tooltip-effect="light"
          v-loading="loading"
        >
          <!-- 列表无数据 -->
          <template slot="empty">
            <el-empty image="/static/img/nodata.png" :image-size="80"></el-empty>
          </template>
          <el-table-column type="index" :index="handleChangeIndex" label="序号" align="center" width="60">
          </el-table-column>
          <el-table-column prop="GeneratorSetName" label="机组名称" min-width="12%">
            <template slot-scope="scope">
              <div>{{ scope.row['GeneratorSetName'] }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="Content" label="描述" min-width="12%">
            <template slot-scope="scope">
              <div>{{ scope.row['Content'] }}</div>
            </template>
          </el-table-column>
          <el-table-column prop="CreateTime" label="时间" min-width="12%">
            <template slot-scope="scope">
              <div>{{ $moment(scope.row['CreateTime']).format('yyyy-MM-DD HH:mm:ss') }}</div>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="160">
            <template slot-scope="scope">
              <div class="table-hover-show-btn">
                <el-button type="text"
                           @click="handleDownLoad(scope.row)">下载</el-button>
                <el-button type="text"
                           @click="handleDelete(scope.row)">删除</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页器 -->
        <div class="pagination-box mt15 mb24">
          <el-pagination
            align="center"
            @size-change="handleChangeSize"
            @current-change="handleChangePage"
            :current-page="paginationCfg.currentPage"
            :page-sizes="paginationCfg.pageSizes"
            :page-size="paginationCfg.pageSize"
            :layout="paginationCfg.layout"
            :total="dataTotal"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增弹窗 -->
    <el-dialog
      :title="operateType == 'add' ? '添加机组履历' : '编辑机组履历'"
      class="history-addedit-dialog"
      width="50%"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="close()"
    >
      <el-form :model="form" :rules="rules" ref="historyForm" :label-width="formLabelWidth">
        <el-form-item label="描述" prop="describe">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入内容"
            v-model="form.describe">
          </el-input>
        </el-form-item>
        <el-form-item label="文件上传" prop="fileList">
          <el-upload
            class="upload-file"
            ref="upload"
            action="#"
            :file-list="form.fileList"
            :auto-upload="false"
            :on-change="getValChange"
            :on-exceed="exceedLimit"
            :on-remove="handleRemove"
            :limit="limit"
            multiple
          >
          <!--  -->
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <!-- <div slot="tip" class="el-upload__tip">
              (支持不超过10M的.doc、docx、xls、xlsx、pdf、rar、zip、jpg、jpge、png文件；最多上传10个文件)
            </div> -->
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('historyForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('historyForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { DownLoadFileByUrl, DownLoadFileByFileReader, DownLoadFileByCreateObjectURL } from '@/utils/util'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import { mapGetters } from 'vuex'
export default {
  name: 'crewHistory',
  mixins: [paginationConfigMixin],
  data() {
    return {
      loading: false,
      tableData: [],
      paginationCfg: {
        pageSizes: [20],
        pageSize: 20
      },
      operateType: null,
      // 弹窗
      dialigVisible: false,
      formLabelWidth: '100px',
      form: {
        describe: '',
        fileList: [],
      },
      rules: {
        describe: [{ required: true, message: '描述', trigger: 'change' }],
        fileList: [{ required: true, message: '请选择文件', trigger: 'change' }],
      },
      limit: 1,

    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.currentPage = 1
        this.queryTableData()
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      const query = {
        page: this.paginationCfg.currentPage,
        pageSize: this.paginationCfg.pageSize
      }
      const parmas = { GeneratorSetId: this.selectCrew.ID }
      this.loading = true
      this.$api.equipment
        .findGenSetInfoResumeByPage(parmas, query)
        .then((res) => {
          console.log(res)
          if (res.IsSuccess) {
            const data = res.Data
            this.tableData = data.Data
            this.dataTotal = data.TotalItemCount
            this.loading = false
          }
        })
        .catch(() => {
          this.loading = false
        })
    },

    
    // 添加
    handleAddHistory() {
      this.operateType = 'add'
      this.dialigVisible = true
    },
    
    exceedLimit(files, fileList) {
      console.log(files, fileList)
      this.$message.error('上传文件个数不能超过' + this.limit + '个')
    },
    // 文件值改变时触发 change事件
    getValChange(file, fileList) {
      console.log('getValChange', file.type, fileList, this.limit)
      const type = file.raw.type
      const isCorrectType = [
        'image/jpeg',
        'image/png',
        'image/bmp',
        'image/gif',
        'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
        'application/vnd.ms-excel',
        'application/pdf',
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        'application/msword',
        'application/vnd.openxmlformats-officedocument.presentationml.presentation',
        'application/vnd.ms-powerpoint'
      ].includes(type)
      const isLt10M = file.size / 1024 <= 1024 * 30
      if (!isCorrectType) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.warning('上传文件格式有误!')
        return false
      }
      if (!isLt10M) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.warning('上传文件大小不能超过 30MB!')
        return false
      }
      this.$set(this.form, 'fileList', fileList)
    },
    handleRemove(file, fileList) {
      this.$set(this.form, 'fileList', fileList)
    },
    
    // 表单提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.form)
          let formData = new FormData()
          this.form.fileList.forEach((item) => {
            formData.append('file', item.raw)
          })

          formData.append('GeneratorSetId', this.selectCrew.ID )
          formData.append('Content', this.form.describe)
          this.$api.equipment.AddGensetInfoResume(formData).then((res) => {
            if (res.Code === 1) {
              this.$message({
                type: 'success',
                message: '文件上传成功!'
              })
              this.dialigVisible = false
              this.queryTableData()
            } else {
              this.$message({
                type: 'error',
                message: '文件上传失败，请重新提交!'
              })
            }

          })
          .catch((error) => {
            this.$message.error(error)
          })
        } else {
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.close()
    },
    close() {
      this.dialigVisible = false
    },

    // 下载
    handleDownLoad(row){
      const params = { Id: row.ResumeId }
      this.$api.equipment.DoDownloadGensetInfoResumeFile(params).then((res) => {
        console.log(res)
        DownLoadFileByCreateObjectURL(res)
      })
      .catch((error) => {
        this.$message.error(error)
      })
    },
    // 删除
    handleDelete(row){
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '移除机组',
        message: h('div', [h('p', [h('span', '确定移除当前机组吗？')])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {

          const params = { Id: row.ResumeId }
          this.$api.equipment.EnableOrDisableGensetInfoResume(params).then((res) => {
            console.log(res)
            if (res.Code == 1){
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
              this.queryTableData()
            } else {
              this.$message({
                type: 'error',
                message: '删除失败，请重新尝试!'
              })
            }
          })
          .catch((error) => {
            this.$message.error(error)
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    }

  }
}
</script>

<style lang="less" scoped>
.crew-history {
  flex: 1;
  .crewinfo-card {
    display: flex;
    flex-direction: column;
    border: 1px solid rgba(0, 0, 0, 0.06);
    border-radius: 4px;
    box-shadow: 0px 2px 1px rgb(0 0 0 / 5%);
    &:not(:last-child) {
      margin-bottom: 24px;
    }
    .crewinfo-card-header {
      padding-left: 4px;
      text-transform: capitalize;
      font-weight: 200;
      letter-spacing: 0.4px;
      line-height: 24px;
      // border-bottom: 1px solid rgba(0, 0, 0, 0.06);
    }
    .crewinfo-card-content {
      flex: 1;
    }
  }
  .operate-btns{
    // text-align: right;
    margin-bottom: 16px;
  }
}
.upload-file{
  /deep/ .el-upload-list{
    &__item{
      background: #fff;
    }
  }
}
</style>



