<!--
 * @Autor: linxu
 * @Date: 2022-02-16 14:21:04
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-31 09:54:33
 * @Description: 出厂试验报告详情 曲线
-->
<template>
  <div class="curve-main">
    <!-- <p class="part-title">突加组合曲线</p>
    <div id="SuddenAdditionCombination"
         class="curve-box"></div>
    <p class="part-title">突卸组合曲线</p>
    <div id="SuddenUnloadingCombination"
         class="curve-box"></div> -->
    <template v-for="(item, index) in combinationArray">
      <p :key="`p_${index}`" class="part-title">{{ item.label }}</p>
      <BaseEcharts
        :key="`echarts_${index}`"
        v-if="combinationFlag"
        class="curve-box"
        :containerId="item.id"
        :colors="combinationColors"
        :grid="combinationGrid"
        :legendData="combinationLegendData[index]"
        :xAxisData="combinationXAxisData"
        :yAxisData="combinationYAxisData[index]"
        :seriesData="combinationSeriesData[index]"
        :toolboxCfg="toolboxCfg"
      />
    </template>

    <!-- 负载突加突卸试验 -->
    <p class="part-title text-left">负载突加突卸试验</p>
    <el-row class="part-content test_part test_part_3">
      <el-col :span="2">
        <p class="line3 text-center"><span>瞬态测试</span></p>
      </el-col>
      <el-col :span="4">
        <p class="text-center"><span>电压(突卸)</span></p>
        <el-row class="test_part__row">
          <el-col :span="12">
            <p class="text-center"><span>瞬态调节率</span></p>
            <p class="text-center">
              <span>{{ transientData && transientData.voltageDischargeRate }}</span>
            </p>
          </el-col>
          <el-col :span="12">
            <p class="text-center"><span>稳定时间(s) </span></p>
            <p class="text-center">
              <span>{{ transientData && transientData.voltageDischargeTime }}</span>
            </p>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="6">
        <p class="text-center"><span>频率(突卸)</span></p>
        <el-row class="test_part__row">
          <el-col :span="12">
            <p class="text-center"><span>瞬态调节率</span></p>
            <p class="text-center">
              <span>{{ transientData && transientData.frequencyDischargeRate }}</span>
            </p>
          </el-col>
          <el-col :span="12">
            <p class="text-center"><span>稳定时间(s) </span></p>
            <p class="text-center">
              <span>{{ transientData && transientData.frequencyDischargeTime }}</span>
            </p>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="6">
        <p class="text-center"><span>电压(突加)</span></p>
        <el-row class="test_part__row">
          <el-col :span="12">
            <p class="text-center"><span>瞬态调节率</span></p>
            <p class="text-center">
              <span>{{ transientData && transientData.voltageAdditionRate }}</span>
            </p>
          </el-col>
          <el-col :span="12">
            <p class="text-center"><span>稳定时间(s) </span></p>
            <p class="text-center">
              <span>{{ transientData && transientData.voltageAdditionTime }}</span>
            </p>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="6">
        <p class="text-center"><span>频率(突加)</span></p>
        <el-row class="test_part__row">
          <el-col :span="12">
            <p class="text-center"><span>瞬态调节率</span></p>
            <p class="text-center">
              <span>{{ transientData && transientData.frequencyAdditionRate }}</span>
            </p>
          </el-col>
          <el-col :span="12">
            <p class="text-center"><span>稳定时间(s) </span></p>
            <p class="text-center">
              <span>{{ transientData && transientData.frequencyAdditionTime }}</span>
            </p>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

    <!-- 稳态电压谐波分量数据图 -->
    <template v-for="item in configList">
      <div :key="item.num" class="curve-box">
        <el-row class="harmonic-content">
          <el-col :span="16">
            <!-- <div :id="`VoltageHarmonicComponent_${item.num}`"></div> -->
            <BaseEcharts
              v-if="flag"
              :key="item.num"
              :containerId="`VoltageHarmonicComponent_${item.num}`"
              :grid="grid"
              :legendData="legendData[item.num]"
              :xAxisData="xAxisData"
              :yAxisData="yAxisData"
              :seriesData="seriesData[item.num]"
              :toolboxCfg="toolboxCfg"
              :tooltip="tooltip"
            />
          </el-col>
          <el-col :span="8">
            <div class="THD-data-container">
              <p>{{ item.lable2 }}: {{ item.THD }}</p>
              <ul class="data-list">
                <li v-for="(num, index) in item.THDArr" :key="index">{{ index }}: {{ num }}</li>
              </ul>
            </div>
          </el-col>
        </el-row>
      </div>
    </template>
  </div>
</template>

<script>
import BaseEcharts from '@/components/BaseEcharts'
// 组合曲线
const combinationArray = [
  // 突加组合曲线
  {
    id: 'SuddenAdditionCombination',
    label: '突加组合曲线',
    echartsItems: [
      {
        label: '突加负载电压',
        accuracy: 10,
        type: 'line',
        yIndex: 1,
        data: []
      },
      {
        label: '突加负载电压频率',
        accuracy: 100,
        type: 'line',
        yIndex: 3,
        data: []
      },
      {
        label: '突加负载转速',
        accuracy: 1,
        type: 'line',
        yIndex: 0,
        data: []
      },
      {
        label: '突加负载电流',
        accuracy: 10,
        type: 'line',
        yIndex: 2,
        data: []
      }
    ],
    yAxisItems: [
      {
        label: 'r/min', // 突加负载转速
        unit: '',
        min: 1300,
        colorIndex: 2
      },
      {
        label: 'V', // 突加负载电压
        unit: '',
        colorIndex: 0
      },
      {
        label: 'A', // 突加负载电流
        unit: '',
        colorIndex: 3
      },
      {
        label: 'Hz', // 突加负载电压频率
        unit: '',
        colorIndex: 1
      }
    ]
  },
  // 突卸组合曲线
  {
    id: 'SuddenUnloadingCombination',
    label: '突卸组合曲线',
    echartsItems: [
      {
        label: '突卸负载电压',
        accuracy: 10,
        type: 'line',
        yIndex: 1,
        data: []
      },
      {
        label: '突卸负载电压频率',
        accuracy: 100,
        type: 'line',
        yIndex: 3,
        data: []
      },
      {
        label: '突卸负载转速',
        accuracy: 1,
        type: 'line',
        yIndex: 0,
        data: []
      },
      {
        label: '突卸负载电流',
        accuracy: 10,
        type: 'line',
        yIndex: 2,
        data: []
      }
    ],
    yAxisItems: [
      {
        label: 'r/min', // 突卸负载转速
        unit: '',
        min: 1300,
        colorIndex: 2
      },
      {
        label: 'V', // 突卸负载电压
        unit: '',
        colorIndex: 0
      },
      {
        label: 'A', // 突卸负载电流
        unit: '',
        colorIndex: 3
      },
      {
        label: 'Hz', // 突卸负载电压频率
        unit: '',
        colorIndex: 1
      }
    ]
  }
]
// 稳态电压谐波分量数据图
const configList = [
  {
    num: 0,
    lable1: '0%稳态电压谐波分量（加载）',
    lable2: '0%稳态电压谐波THD（加载）',
    THD: '',
    THDArr: []
  },
  {
    num: 1,
    lable1: '25%稳态电压谐波分量（加载）',
    lable2: '25%稳态电压谐波THD（加载）',
    THD: '',
    THDArr: []
  },
  {
    num: 2,
    lable1: '50%稳态电压谐波分量（加载）',
    lable2: '50%稳态电压谐波THD（加载）',
    THD: '',
    THDArr: []
  },
  {
    num: 3,
    lable1: '75%稳态电压谐波分量（加载）',
    lable2: '75%稳态电压谐波THD（加载）',
    THD: '',
    THDArr: []
  },
  {
    num: 4,
    lable1: '100%稳态电压谐波分量（加载）',
    lable2: '100%稳态电压谐波THD（加载）',
    THD: '',
    THDArr: []
  },
  {
    num: 5,
    lable1: '110%稳态电压谐波分量（加载）',
    lable2: '110%稳态电压谐波THD（加载）',
    THD: '',
    THDArr: []
  },
  {
    num: 6,
    lable1: '100%稳态电压谐波分量（减载）',
    lable2: '100%稳态电压谐波THD（减载）',
    THD: '',
    THDArr: []
  },
  {
    num: 7,
    lable1: '75%稳态电压谐波分量（减载）',
    lable2: '75%稳态电压谐波THD（减载）',
    THD: '',
    THDArr: []
  },
  {
    num: 8,
    lable1: '50%稳态电压谐波分量（减载）',
    lable2: '50%稳态电压谐波THD（减载）',
    THD: '',
    THDArr: []
  },
  {
    num: 9,
    lable1: '25%稳态电压谐波分量（减载）',
    lable2: '25%稳态电压谐波THD（减载）',
    THD: '',
    THDArr: []
  },
  {
    num: 10,
    lable1: '0%稳态电压谐波分量（减载）',
    lable2: '0%稳态电压谐波THD（减载）',
    THD: '',
    THDArr: []
  }
]
const xData = (function () {
  let arr = []
  for (let i = 0; i < 32; i++) {
    arr.push(i)
  }
  return arr
})()
const option = {
  legend: {
    data: ['0%稳态电压谐波分量（加载）']
  },
  xAxis: [
    {
      type: 'category',
      name: 'Time(s)',
      axisLabel: {
        interval: 0,
        rotate: -45,
        align: 'center',
        margin: 14
      },
      axisTick: {
        alignWithLabel: true
      },
      data: xData
    }
  ],
  yAxis: [
    {
      type: 'value',
      name: '%',
      max: 100,
      axisLine: {
        show: true
      },
      axisTick: {
        show: true
      },
      splitLine: {
        lineStyle: {
          type: 'dashed',
          color: ''
        }
      }
    }
  ],
  series: [
    {
      data: [],
      type: 'bar',
      name: '0%稳态电压谐波分量（加载）'
    }
  ]
}
export default {
  name: 'DeliveryTestReportDetailCurve',
  components: {
    BaseEcharts
  },
  props: ['data'],
  data() {
    return {
      Data: this.data,
      configList,
      toolboxCfg: {
        dataViewIsShow: false,
        magicTypeIsShow: false,
        restoreIsShow: false,
        saveAsImageIsShow: true
      },
      combinationArray,
      // 组合曲线
      combinationFlag: false,
      combinationGrid: {
        x: 50,
        y: 24,
        x2: 150,
        y2: 24,
        top: '15%'
      },
      combinationColors: ['#cad400', '#ef7b10', '#56d43e', '#bd2b0b'],
      combinationLegendData: [],
      combinationXAxisData: [],
      combinationYAxisData: [],
      combinationSeriesData: [],

      transientData: null,
      // 稳态电压谐波分量数据图
      flag: false,
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow'
        }
      },
      grid: {
        left: '40',
        right: '60',
        top: '15%'
      },
      legendData: [],
      xAxisData: [
        {
          type: 'category',
          name: 'Time(s)',
          axisLabel: {
            interval: 0,
            rotate: -45,
            align: 'center',
            margin: 14
          },
          axisTick: {
            alignWithLabel: true
          },
          data: xData
        }
      ],
      yAxisData: [
        {
          type: 'value',
          name: '%',
          max: 100,
          axisLine: {
            show: true
          },
          axisTick: {
            show: true
          }
        }
      ],
      seriesData: []
    }
  },
  watch: {
    data: {
      handler(newVal, oldVal) {
        this.Data = newVal
        this.transientDataInit()
        this.combinationInit()
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.transientDataInit()
    this.combinationInit()
    this.init()
  },
  methods: {
    transientDataInit() {
      if (this.Data) {
        this.transientData = {
          voltageDischargeRate: this.Data['电压突卸瞬态调节率'] / 100 + '%',
          voltageDischargeTime: this.Data['电压突卸稳定时间'] / 10,
          frequencyDischargeRate: this.Data['频率突卸瞬态调节率'] / 100 + '%',
          frequencyDischargeTime: this.Data['频率突卸稳定时间'] / 10,
          voltageAdditionRate: this.Data['电压突加瞬态调节率'] / 100 + '%',
          voltageAdditionTime: this.Data['电压突加稳定时间'] / 10,
          frequencyAdditionRate: this.Data['频率突加瞬态调节率'] / 100 + '%',
          frequencyAdditionTime: this.Data['频率突加稳定时间'] / 10
        }
      }
    },
    getXData() {
      let data = []
      for (let i = 0; i <= 10; i++) {
        for (let j = 0; j < 10; j++) {
          if (i !== 10) {
            let floatNum = j === 0 ? i : i + '.' + j
            data.push(floatNum)
          } else {
            data.push(i)
            break
          }
        }
      }
      return data
    },
    // 组合曲线初始化
    combinationInit() {
      this.combinationXAxisData = [
        {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {
            interval: 4
          },
          splitLine: {
            show: true,
            width: 5,
            lineStyle: {
              type: 'dotted',
              color: '#6E7079'
            }
          },
          boundaryGap: false,
          data: this.getXData()
        }
      ]
      const combinationLegendData = []
      const combinationYAxisData = []
      const combinationSeriesData = []

      this.combinationArray.forEach((fItem) => {
        const legendDataItem = []
        const yAxisDataItem = []
        const seriesDataItem = []
        fItem.echartsItems.forEach((item, index) => {
          // 设置legendData
          legendDataItem.push(item.label)

          // 设置seriesData
          const seriesDataChildItem = {
            name: item.label,
            type: item.type,
            yAxisIndex: item.yIndex,
            data:
              this.Data[item.label] == null
                ? []
                : this.Data[item.label].split(',').map((data) => {
                    return +data / item.accuracy
                  }),
            showAllSymbol: false
          }
          seriesDataItem.push(seriesDataChildItem)
        })
        fItem.yAxisItems.forEach((item, index) => {
          // yAxisData
          const yAxisDataChildItem = {
            type: 'value',
            name: item.label,
            min: item.min || 0,
            // max: 250,
            position: index === 0 ? 'left' : 'right',
            offset: index >= 2 ? (index - 1) * 50 : 0,
            axisLine: {
              show: true,
              lineStyle: {
                color: this.combinationColors[item.colorIndex]
              }
            },
            axisLabel: {
              formatter: `{value} ${item.unit || ''}`
            },
            splitLine: {
              lineStyle: {
                type: 'dotted',
                color: '#6E7079'
              }
            }
          }
          yAxisDataItem.push(yAxisDataChildItem)
        })
        combinationLegendData.push(legendDataItem)
        combinationYAxisData.push(yAxisDataItem)
        combinationSeriesData.push(seriesDataItem)
      })
      // console.log(combinationLegendData, combinationYAxisData, combinationSeriesData)
      this.$set(this, 'combinationLegendData', combinationLegendData)
      this.$set(this, 'combinationYAxisData', combinationYAxisData)
      this.$set(this, 'combinationSeriesData', combinationSeriesData)
      this.$nextTick(() => {
        this.combinationFlag = true
      })
    },
    // 稳态电压谐波分量数据图初始化
    init() {
      const legendData = []
      const seriesData = []
      configList.forEach((item, index) => {
        item.THD = this.Data[item.lable2] == null ? '' : this.Data[item.lable2] / 100 + '%'

        let dataArr =
          this.Data[item.lable1] == null
            ? []
            : this.Data[item.lable1]
                .split(',')
                .map((data) => {
                  return +data / 100
                })
                .slice(0, 32)
        item.THDArr = dataArr.map((data) => {
          return data + '%'
        })
        // console.log(item.THD,this.Data[item.lable2] / 100)
        legendData.push([item.lable1])
        seriesData.push([
          {
            data: dataArr,
            type: 'bar',
            name: item.lable1
          }
        ])
      })
      this.$set(this, 'legendData', legendData)
      this.$set(this, 'seriesData', seriesData)
      this.$nextTick(() => {
        this.flag = true
      })
    }
  }
}
</script>

<style lang="less" scoped>
@borderColor: #ebeef5;
@borderTop: {
  border-top: 1px solid @borderColor;
};
@borderBottom: {
  border-bottom: 1px solid @borderColor;
};
.curve-main {
  /deep/.curve-box {
    height: 350px;
    padding: 5px 4px;
    &:not(:last-child) {
      @borderBottom();
    }
  }
  .harmonic-content {
    height: 100%;
    .el-col {
      height: 100%;
      padding: 0 14px;
      div {
        height: 100%;
      }
    }
    .THD-data-container {
      p {
        padding: 4px 0;
      }
    }
    .data-list {
      display: flex;
      flex-wrap: wrap;
      li {
        width: 25%;
        line-height: 28px;
      }
    }
  }
}
</style>



