<!--
 * @Autor: linxu
 * @Date: 2022-01-18 16:31:34
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-26 15:46:23
 * @Description: 运行可靠性报告
-->
<template>
  <div class="operational-reliability-report">
    <el-descriptions class="alarm-descriptions-items" title="" :column="1" border>
      <el-descriptions-item>
        <template slot="label">停机类报警</template>
        <div class="description-content-box">
          <span>{{ dataSource && dataSource.TotalYearAllAlarm }} 次</span>
        </div>
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">警告类报警</template>
        <div class="description-content-box">
          <span>{{ dataSource && dataSource.TotalYearAllWarn }} 次</span>
        </div>
      </el-descriptions-item>
    </el-descriptions>

    <!-- 报警信息图例 -->
    <BaseEcharts
      v-if="flag"
      :containerId="containerId"
      :colors="colors"
      :grid="grid"
      :legendData="legendData"
      :xAxisData="xAxisData"
      :yAxisData="yAxisData"
      :seriesData="seriesData"
      :toolboxCfg="toolboxCfg"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import BaseEcharts from '@/components/BaseEcharts'
import OperationReportComponent from './OperationReportComponent'
const colors = ['#5470C6', '#91CC75']
const echartsItems = [
  {
    label: '停机类报警',
    key: 'TotalAlarmCount',
    type: 'bar',
    unit: 'kwh',
    data: [2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3]
  },
  {
    label: '警告类报警',
    key: 'TotalWarnCount',
    type: 'bar',
    data: [2.0, 2.2, 3.3, 4.5, 6.3, 10.2, 20.3, 23.4, 23.0, 16.5, 12.0, 6.2]
  }
]
export default {
  name: 'OperationalReliabilityReport',
  components: {
    BaseEcharts,
    OperationReportComponent
  },
  props: ['year'],
  data() {
    return {
      dataSource: null,
      // year: new Date().getFullYear().toString(),
      title: '机组运行可靠性报告',
      flag: false, // 更新 BaseEcharts 组件

      toolboxCfg: {
        dataViewIsShow: true,
        magicTypeIsShow: true,
        restoreIsShow: true,
        saveAsImageIsShow: true
      },
      containerId: 'alarm-report-echart',
      colors,
      grid: {
        left: '5%',
        right: '8%',
        top: '15%'
      },
      legendData: [],
      xAxisData: [],
      yAxisData: [],
      seriesData: [],

      data: null
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        this.getData()
      },
      // 深度观察监听
      deep: true
    },
    year(newVal, oldVal) {
      this.getData()
    },
  },
  mounted() {
    this.getData()
  },
  methods: {
    async getData() {
      const params = {
        GeneratorSetId: this.selectCrew.ID,
        Year: this.year,
        StartTime: `${this.year}-01`
        // EndTime: dayjs(new Date()).format('YYYY-MM-DD')
      }
      await this.$api.equipment
        .GetGensetAlarmReport(params, {})
        .then((res) => {
          if (res.Code === 1) {
            this.$set(this, 'data', res.Data)
            this.$set(this, 'dataSource', {
              ...res.Data.Genset,
              TotalYearAllAlarm: res.Data.TotalYearAllAlarm,
              TotalYearAllWarn: res.Data.TotalYearAllWarn
            })
          }
        })
        .catch(() => {})

      this.init()
    },
    init() {
      console.log(this.data)
      this.xAxisData = [
        {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
          data: this.data.TotalMonth
        }
      ]
      this.yAxisData = [
        {
          type: 'value',
          name: '数量',
          min: 0,
          minInterval: 1,
          // max: 250,
          axisLine: {
            show: true,
            lineStyle: {
              color: colors[0]
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#6E7079',
              opacity: 0.4
            }
          },
        }
      ]

      const legendData = []
      const seriesData = []
      echartsItems.forEach((item, index) => {
        legendData.push(item.label)
        // 设置yAxisData
        // const yAxisDataItem = {
        //   type: 'value',
        //   name: item.label,
        //   min: 0,
        //   // max: 250,
        //   position: index === 0 ? 'left' : 'right',
        //   offset: index >= 2 ? (index - 1) * 60 : 0,
        //   axisLine: {
        //     show: true,
        //     lineStyle: {
        //       color: colors[0]
        //     }
        //   },
        //   axisLabel: {
        //     formatter: `{value} ${item.unit || ''}`
        //   }
        // }
        // yAxisData.push(yAxisDataItem)
        // 设置seriesData
        const seriesDataItem = {
          name: item.label,
          type: item.type,
          data: this.data[item.key]
        }
        seriesData.push(seriesDataItem)
      })
      this.$set(this, 'legendData', legendData)
      this.$set(this, 'seriesData', seriesData)
      this.flag = true
    }
  }
}
</script>

<style lang="less" scoped>
.operational-reliability-report {
  /deep/.alarm-descriptions-items {
    margin: 16px 0;
    .el-descriptions-item__cell {
      text-align: center;
    }
    td.el-descriptions-item__cell {
      width: 85%;
    }
  }
  #alarm-report-echart {
    height: 560px;
  }
}
</style>



