<!--
 * @Autor: linxu
 * @Date: 2022-08-17 10:39:06
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-09 17:06:35
 * @Description: 机组详情页面
-->
<template>
  <div>
    <el-container>
      <el-main>
        <div id="genset-mould-page">
          <genset-info
            ref="gensetInfo"
            :gensetInfo="GeneratorSetInfo"
            :hasBackBtn="hasBackBtn"
            @handleRefresh="handleRefresh"
          ></genset-info>

          <el-tabs v-model="activeName" @tab-click="handleClickTab" class="genset-tabs">
            <template v-for="item in tabList">
              <el-tab-pane :key="item.key" :label="item.value" :name="item.key"></el-tab-pane>
            </template>
          </el-tabs>

          <!-- 动态组件 -->
          <component :is="currentView" ref="subComponent" @changeGeneratorSetInfo="changeGeneratorSetInfo"></component>

          <!-- 标签对应组件引入 -->
          <!-- 机组信息 -->
          <!-- <crewinfo-detail v-if="activeName == 1"></crewinfo-detail> -->
          <!-- 机组配置 -->
          <!-- <genset-config v-if="activeName == 2"></genset-config> -->
          <!-- 运行监控 -->
          <!-- <crew-monitor v-if="activeName == 3"></crew-monitor> -->
          <!-- 维保监控 -->
          <!-- <maintenance-monitor
            v-if="activeName == 4"
            @changeGeneratorSetInfo="changeGeneratorSetInfo"
          ></maintenance-monitor> -->

          <!-- 劣化分析 -->
          <!-- <deterioration-analysis v-if="activeName == 5"></deterioration-analysis> -->
          <!-- 远程解锁机 -->
          <!-- <genset-lock v-if="activeName == 6"></genset-lock> -->
          <!-- 云消息推送 -->
          <!-- <genset-cloud-message v-if="activeName == 7"></genset-cloud-message> -->
          <!-- 机组报告 -->
          <!-- <crewreport-detail v-if="activeName == 8"></crewreport-detail> -->
          <!-- 数据记录 -->
          <!-- <crew-data-record v-if="activeName == 9"></crew-data-record> -->
        </div>
      </el-main>
      <toggle-crew></toggle-crew>
    </el-container>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import GensetInfo from './components/GensetInfo'
import ToggleCrew from '@/components/toggleCrew/list'
import { formatTime } from '@/utils/common'

import CrewinfoDetail from '@/views/equipmentManage/crewinfo/crewinfoDetail'
import GensetConfig from '@/views/equipmentManage/gensetConfig'
import CrewMonitor from '@/views/equipmentManage/crew-monitor'
import MaintenanceMonitor from '../operation/maintenanceMonitor'
import DeteriorationAnalysis from '@/views/operation/deteriorationAnalysis'
import GensetLock from '@/views/operation/gensetLock'
import GensetCloudMessage from '@/views/operation/gensetCloudMessage'
import CrewreportDetail from '@/views/equipmentManage/crewinfo/crewreportDetail'
import CrewDataRecord from '@/views/equipmentManage/crew-record'

const tabList = [
  { key: '1', value: '机组信息', componentName: 'CrewinfoDetail' },
  { key: '2', value: '机组配置', componentName: 'GensetConfig' },
  { key: '3', value: '运行监控', componentName: 'CrewMonitor' },
  { key: '4', value: '维保监控', componentName: 'MaintenanceMonitor' },
  { key: '5', value: '劣化分析', componentName: 'DeteriorationAnalysis' },
  { key: '6', value: '远程解锁机', componentName: 'GensetLock' },
  { key: '7', value: '云消息推送', componentName: 'GensetCloudMessage' },
  { key: '8', value: '机组报告', componentName: 'CrewreportDetail' }
  // { key: '9', value: '数据记录', componentName: 'CrewDataRecord'  },
]
export default {
  name: 'sensorMonitor',
  inject: ['reload'],
  components: {
    GensetInfo,
    ToggleCrew,

    CrewinfoDetail,
    GensetConfig,
    CrewMonitor,
    MaintenanceMonitor,
    DeteriorationAnalysis,
    GensetLock,
    GensetCloudMessage,
    CrewreportDetail,
    CrewDataRecord
  },
  beforeRouteEnter(to, from, next) {
    // console.log('beforeRouteEnter', from)
    next((vm) => {
      if (
        from.name === 'crewinfo' ||
        from.name == 'operationalChartDaily' ||
        from.name == 'operationalChartWeekly'
      ) {
        vm.hasBackBtn = true
      }
    })
  },
  watch: {
    $route: {
      handler: function (nv, ov) {
        // console.log('watch', nv, this.$route.query.active)
        // 通过路由定位当前页签
        this.activeName = this.$route.query.active || nv.meta.activeNum
        this.currentView = this.tabList.filter(
          (item) => item.key == this.activeName
        )[0].componentName
      },
      deep: true,
      immediate: true
    }
  },
  data() {
    return {
      hasBackBtn: false,
      GensetId: null,
      GeneratorSetInfo: {},
      tabList,
      activeName: '1',
      currentView: ''
    }
  },
  mounted() {},
  methods: {
    changeGeneratorSetInfo(val) {
      this.$set(this, 'GeneratorSetInfo', val)
    },
    handleClickTab() {
      this.currentView = this.tabList.filter((item) => item.key == this.activeName)[0].componentName
    },
    handleRefresh() {
      this.$refs.gensetInfo.getGeneratorSet()
      this.$refs.subComponent.initData && this.$refs.subComponent.initData()
    }
  },
  beforeDestroy() {}
}
</script>

<style lang="less" scoped>
#genset-mould-page {
  .genset-tabs {
    margin-bottom: 9px;
  }
  /deep/ .el-container {
    padding: 0 0 16px 0 !important;
  }
}
.theme-dark {
  .maintenance-record-dialog {
    /deep/ .el-timeline {
      .el-timeline-item__timestamp {
        color: #fff;
      }
    }
  }
}
.theme-light {
  .maintenance-matter {
    li.title {
      background: #f3f3f3 !important;
    }
  }
}
</style>



