<!--
 * @Author: linxu
 * @Date: 2021-11-18 13:40:38
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-21 09:34:39
 * @Description: 机组信息详情内容
-->
<template>
  <div id="crewinfo-box" v-loading="loading">
    <template v-if="data">
      <div class="crewinfo-card" v-for="(item, index) in dataList" :key="index">
        <div class="crewinfo-card-header">{{ item.title }}</div>
        <div class="crewinfo-card-content">
          <div
            class="crewinfo-card-content__item"
            v-for="(cItem, cIndex) in item.options"
            :key="cIndex"
          >
            <ul v-if="cItem.type == 'ul'">
              <li v-for="(option, i) in cItem.options" :key="i" :class="option.className">
                <span>{{ option.label }}</span>
                <template v-if="option.key === 'ImgUrl'">
                  <el-image
                    v-if="data && data[option.key]"
                    fit="contain"
                    :style="{
                      height: '100%',
                      width: '220px'
                    }"
                    :src="imgUrl(data[option.key])"
                  />
                  <em v-else>{{ data[option.key] || '未知' }}</em>
                </template>
                <em v-else>{{ data[option.key] || '未知' }}</em>
              </li>
            </ul>
            <ol v-else>
              <!-- <li v-for="(option, i) in cItem.options" :key="i" :class="option.className">
                <span>{{option.label}}</span><em>{{data[option.key] || '未知'}}</em>
              </li> -->
              <li>
                <span>可编程传感器 1</span><span>显示名:</span><em>温度</em><span>单位:</span
                ><em>℃</em>
              </li>
              <li>
                <span>可编程传感器 2</span><span>显示名:</span><em>油压</em><span>单位:</span
                ><em>kPa</em>
              </li>
              <li>
                <span>可编程传感器 3</span><span>显示名:</span><em>液位</em><span>单位:</span
                ><em>%</em>
              </li>
            </ol>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { handlePicUrl } from '@/utils/common'
import { mapGetters } from 'vuex'
export default {
  name: 'crewinfoDetail',
  components: {},
  data() {
    return {
      loading: false,
      data: null,
      dataList: [
        {
          title: '基本信息',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'DisplayName', label: '机组名称' },
                { key: 'CollectorToken', label: '云猫标识' },
                { key: 'Longitude', label: '经度' },
                { key: 'Latitude', label: '纬度' },
                { key: 'InstallationSite', label: '安装地点', className: 'w100' },
                { key: 'IsCurve', label: '曲线' },
                { key: 'IsOnduty', label: '有人值守' }
              ]
            }
          ]
        },
        {
          title: '机组信息',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'Serial', label: '出厂编号' },
                { key: 'Brand', label: '品牌' },
                { key: 'Model', label: '型号' },
                { key: 'Standard', label: '规格' },
                { key: 'Manufacturer', label: '生产厂家' },
                { key: 'ManufactureDate', label: '生产日期' },
                { key: 'ReMark', label: '备注' }
              ]
            },
            {
              type: 'ul',
              options: [
                { key: 'RatedSpeed', label: '额定转速(r/min)' },
                { key: 'RatedFrequency', label: '额定频率(Hz)' },
                { key: 'RatedCurrent', label: '额定电流(A)' },
                { key: 'RatedPower', label: '额定功率(kW)' },
                { key: 'RatedVoltage', label: '额定电压(V)' },
                { key: 'ApparentPower', label: '视在功率(kVA)' },
                { key: 'PowerFactor', label: '功率因数' }
              ]
            },
            {
              type: 'ul',
              options: [{ key: 'ImgUrl', label: '机组照片' }]
            }
          ]
        },
        {
          title: '维保信息',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'GeneratorSetClientNumber', label: '客户编号' },
                { key: 'GeneratorSetClientName', label: '客户公司' },
                { key: 'GensetCustomerName', label: '设备归属公司', className: 'w100' },
                { key: 'GensetUserName', label: '设备责任人' },
                { key: 'GensetUserPhone', label: '责任人联系电话' },
                { key: 'MaintenCustomerName', label: '维保公司', className: 'w100' },
                { key: 'MaintenUserName', label: '维保联系人' },
                { key: 'MaintenUserPhone', label: '联系人电话' }
              ]
            }
          ]
        },
        {
          title: '控制器信息',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'ControllerSerial', label: '出厂编号' },
                { key: 'ControllerBrand', label: '品牌' },
                { key: 'ControllerModel', label: '型号' },
                { key: 'ControllerStandard', label: '规格' },
                { key: 'ControllerManufacturer', label: '生产厂家' },
                { key: 'ControllerManufactureDate', label: '生产日期' }
              ]
            },
            {
              type: 'ul',
              options: [
                { key: 'ControllerType', label: '控制器类型' },
                { key: 'IsControl', label: '是否可以控制' },
                { key: 'VoltageType', label: '供电系统' }
              ]
            },
            {
              type: 'ol',
              options: [
                // ProgrammableSensors [{Name:'', Unit: ''}]
                { key: '', label: '可编程传感器 1' },
                { key: '', label: '可编程传感器 2' },
                { key: '', label: '可编程传感器 3' }
              ]
            }
          ]
        },
        {
          title: '发动机信息',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'EngineSerial', label: '出厂编号' },
                { key: 'EngineBrand', label: '品牌' },
                { key: 'EngineModel', label: '型号' },
                { key: 'EngineStandard', label: '规格' },
                { key: 'EngineManufacturer', label: '生产厂家' },
                { key: 'EngineManufactureDate', label: '生产日期' }
              ]
            }
          ]
        },
        {
          title: '发电机信息',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'GeneratorSerial', label: '出厂编号' },
                { key: 'GeneratorBrand', label: '品牌' },
                { key: 'GeneratorModel', label: '型号' },
                { key: 'GeneratorStandard', label: '规格' },
                { key: 'GeneratorManufacturer', label: '生产厂家' },
                { key: 'GeneratorManufactureDate', label: '生产日期' }
              ]
            }
          ]
        },
        {
          title: '采集器信息',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'CollectorSerial', label: '出厂编号' },
                { key: 'CollectorBrand', label: '品牌' },
                { key: 'CollectorModel', label: '型号' },
                { key: 'CollectorStandard', label: '规格' },
                { key: 'CollectorManufacturer', label: '生产厂家' },
                { key: 'CollectorManufactureDate', label: '生产日期' },
                { key: 'Editstatus', label: '通讯协议状态' },
                { key: 'CollectorTip', label: '通讯说明' }
              ]
            }
          ]
        },
        {
          title: '易耗配件材料统计',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'InspectionCardNumber', label: '巡检卡号', className: 'w100' },
                { key: 'EngineOil', label: '防冻液容量' },
                { key: 'EngineOilCapacityStr', label: '机油容量' },
                { key: 'AirFilterModel', label: '空气滤清器型号' },
                { key: 'AirFilterQuantityStr', label: '个数' },
                { key: 'OilFilterModel', label: '机油滤清器型号' },
                { key: 'OilFilterQuantityStr', label: '个数' },
                { key: 'OilFilterSideModel', label: '机油旁滤清器型号' },
                { key: 'OilFilterSideQuantityStr', label: '个数' },
                { key: 'DieselFilterModel', label: '柴油滤清器型号' },
                { key: 'DieselFilterQuantityStr', label: '个数' }
                // {key: 'WaterFilterModel', label: '水滤清器型号'},
                // {key: 'WaterFilterQuantityStr', label: '个数'}
              ]
            }
          ]
        },
        {
          title: '基站信息',
          options: [
            {
              type: 'ul',
              options: [
                { key: 'BaseStationName', label: '基站名称' },
                { key: 'BaseStationNumber', label: '基站编号' },
                { key: 'BaseStationAddress', label: '基站地点' }
              ]
            }
          ]
        }
      ]
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (newVal, oldVal) {
        // console.log(newVal, oldVal)
        this.getsingleMchInfo()
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    this.getsingleMchInfo()
  },
  methods: {
    getsingleMchInfo() {
      this.loading = true
      this.$api.operation
        .FindGeneratorSet({ id: this.selectCrew.ID })
        .then((res) => {
          console.log(res)
          if (res.IsSuccess) {
            const obj = res.Data
            obj.IsCurve = obj.IsCurve ? '是' : '否' // 基本信息-曲线
            obj.IsOnduty = obj.IsOnduty ? '是' : '否' // 基本信息-有人值守
            obj.ControllerType = obj.ControllerType === 0 ? 'AMF模块' : '未知' // 控制器信息
            obj.IsControl = obj.IsControl ? '是' : '否'
            obj.VoltageType =
              obj.VoltageType === 1 ? '1P2W' : obj.VoltageType === 3 ? '3P3W' : '未知'
            obj.Editstatus =
              obj.Editstatus === 0 ? '已完成' : obj.status === 0 ? '待下发' : '下发中' // 采集器信息
            obj.CollectorTip = obj.CollectorTip || '通讯正常'

            // 日期格式转换
            obj.ManufactureDate = obj.ManufactureDate
              ? this.$moment(obj.ManufactureDate).format('yyyy-MM-DD')
              : ''
            obj.ControllerManufactureDate = obj.ControllerManufactureDate
              ? this.$moment(obj.ControllerManufactureDate).format('yyyy-MM-DD')
              : ''
            obj.EngineManufactureDate = obj.EngineManufactureDate
              ? this.$moment(obj.EngineManufactureDate).format('yyyy-MM-DD')
              : ''
            obj.GeneratorManufactureDate = obj.GeneratorManufactureDate
              ? this.$moment(obj.GeneratorManufactureDate).format('yyyy-MM-DD')
              : ''
            obj.CollectorManufactureDate = obj.CollectorManufactureDate
              ? this.$moment(obj.CollectorManufactureDate).format('yyyy-MM-DD')
              : ''

            this.data = obj
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    imgUrl(url) {
      return handlePicUrl(url, 'old')
    }
  }
}
</script>

<style lang="less" scoped>
#crewinfo-box {
  min-height: 200px;
  // padding-bottom: 16px;
  .crewinfo-card {
    border: 1px solid #e4e4e4;
    border-radius: 4px;
    box-shadow: 0px 2px 1px rgb(0 0 0 / 5%);
    &:not(:last-child) {
      margin-bottom: 24px;
    }
    .crewinfo-card-header {
      padding-left: 4px;
      text-transform: capitalize;
      font-weight: 200;
      letter-spacing: 0.4px;
      line-height: 24px;
    }
    .crewinfo-card-content {
      .crewinfo-card-content__item {
        padding: 16px 0;
        min-height: 40px;
        border-top: 1px solid #e4e4e4;
        ul {
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          li {
            width: 50%;
            line-height: 32px;
            display: flex;
            justify-content: flex-start;
            &.w100 {
              width: 100%;
              span {
                width: 16.5%;
              }
            }
            span {
              width: 33%;
              text-align: right;
              // color: #414141;
              font-weight: bold;
              padding-right: 8px;
            }
            em {
              flex: 1;
              // color: #373a3c;
              padding-left: 8px;
            }
          }
        }
        ol {
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          li {
            margin-right: 24px;
            width: 50%;
            line-height: 32px;
            display: flex;
            justify-content: flex-start;
            span {
              width: 10%;
              text-align: right;
              // color: #414141;
              font-weight: bold;
              padding-right: 8px;
              &:first-child {
                width: 33%;
              }
            }
            em {
              flex: 1;
              // color: #373a3c;
              padding-left: 8px;
            }
          }
        }
      }
    }
  }
}
</style>



